@import 'ScssHelpers/helpers-all.scss';

.mobile_slider {
	@include bp($point_3) {
		position: relative;

		// margin: 0 calc(var(--gl-indent) * -1);
	}

	.swiper {
		overflow: visible;
	}

	.swiper-wrapper {
		overflow: visible;
		@include bp($point_2, min) {
			flex-wrap: wrap;
		}

		// @include bp($point_3) {
		// 	padding: 0 var(--gl-indent);
		// }
	}
}

.swiper-wrapper {
	.recent_projects__list & {
		@include bp($point_2, min) {
			@include flex_block(row, wrap, flex-start, center);

			margin: 0 -4rem -3.5rem 0;
			width: auto;
		}
	}

	.news & {
		@include bp($point_2, min) {
			@include flex_block(row, wrap, flex-start, stretch, stretch);

			margin: 0 -4rem -4rem 0;
		}
	}
}

.swiper-slide {
	.recent_projects__list &,
	.contract & {
		@include bp($point_2, min) {
			width: 33.33%;
			padding: 0 4rem 3.5rem 0;
		}
	}

	.news & {
		@include bp($point_4, min) {
			width: 50%;
			flex-shrink: 0;

			padding: 0 4rem 4rem 0;
		}
	}
}

.mobile_slider__controls {
	@include bp($point_2, min) {
		display: none;
	}
}

.mobile_slider__controls_item {
	position: absolute;
	top: 50%;

	width: 1.6rem;
	height: 3.2rem;
	margin-top: -3.2rem;

	color: $white;
	filter: drop-shadow(0 0 7px rgba(0, 0, 0, 0.6));

	z-index: 5;

	&:before {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;

		width: 4.8rem;
		height: 8.8rem;
		margin: -4.4rem 0 0 -2.4rem;
	}

	.mobile_slider__controls--dark_mod & {
		color: $black;
	}

	&.swiper-button-lock,
	&.swiper-button-disabled {
		opacity: 0;
		pointer-events: none;
	}

	&.mobile_slider__controls_item--prev_mod {
		left: var(--gl-indent);
		left: 0;
	}

	&.mobile_slider__controls_item--next_mod {
		right: var(--gl-indent);
		right: 0;
	}
}
