@import 'ScssHelpers/helpers-all.scss';

.lifestyle_section {
}

.lifestyle__list {
	display: flex;
	flex-wrap: wrap;
	@include bp($point_2, min) {
		margin: 0 -4rem -4.2rem 0;
	}
}

.lifestyle {
	@include bp($point_2, min) {
		padding: 0 4rem 4.2rem 0;
		width: 25%;

		.lifestyle__list:not(.lifestyle__list--v2_mod) & {
			&:nth-child(6n + 2),
			&:nth-child(6n + 4) {
				width: 50%;
			}
		}

		.lifestyle__list--v2_mod & {
			&:nth-child(6n + 1),
			&:nth-child(6n + 6) {
				width: 50%;
			}
		}
	}

	@include bp($point_3) {
		&.swiper-slide {
			width: auto;
			max-width: calc(100vw - var(--gl-indent) * 2);
		}

		&:not(:last-child) {
			margin-right: 2rem;
		}
	}

	@include bp($point_4 - 1) {
		&:first-child:last-child {
			width: 100%;
		}
	}
}

.lifestyle__link {
	color: $black;

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

.lifestyle__pic {
	position: relative;
	display: block;
	margin-bottom: 1.2rem;
	overflow: hidden;

	@include bp($point_2, min) {
		height: 49.6rem;

		.lifestyle__list--height_mod & {
			height: 47.4rem;
		}
	}

	@include bp($point_3) {
		height: 31.8rem;
	}
}

.lifestyle__img {
	@include cover_img;
	@include hover_img('.lifestyle__link');

	.mobile_slider & {
		@include bp($point_3) {
			width: auto;
		}
	}
}

.lifestyle__title {
	@include text_2;
}
