@import 'ScssHelpers/helpers-all.scss';

.designer {
	// padding-top: 10rem;

	@include bp($point_3) {
		padding-bottom: 3rem;
	}
}

.designer__descr {
	@include text;

	@include bp($point_2, min) {
		margin-bottom: 4.6rem;
	}

	@include bp($point_3) {
		margin-bottom: 2.6rem;
	}
}

.designer__images_list {
	@include bp($point_2, min) {
		margin: 0 -4rem -4rem 0;
		@include flex_block(row, wrap, flex-start, flex-start, flex-start);
	}
}

.designer__images_list_item {
	@include bp($point_2, min) {
		width: 50%;
		padding: 0 4rem 4rem 0;
	}

	@include bp($point_3) {
		width: 100%;

		&:not(:last-child) {
			margin-bottom: 2.1rem;
		}
	}
}

.designer__images_list_picture {
	display: block;
	width: 100%;
	height: 100%;
}

.designer__images_list_img {
	@include cover_img;
}
