.info {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 9.6rem;
		}
	}

	@include bp($point_3 - 1) {
		&:not(:last-child) {
			margin-bottom: 2.6rem;
		}
	}
}

.info__list {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}

	@include bp($point_3 - 1) {
		@include flex_block(row, wrap, flex-start, stretch);

		margin-bottom: -1rem;
	}
}

.info__item {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 2.8rem;
		}
	}

	@include bp($point_3) {
		width: 50%;
		flex-shrink: 0;

		display: flex;
		flex-direction: column;

		padding-bottom: 1rem;
	}

	&:nth-child(2n + 2) {
		@include bp($point_3) {
			padding-left: 1rem;
		}
	}
}

.info__item_title {
	@include text;

	font-weight: 400;
}

.info__item_descr {
	@include text;
}

.info__sub_list {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 2.8rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 2.2rem;
		}
	}
}

.info__sub_item {
}

.info__sub__link {
	position: relative;

	@include text;

	color: $black;

	&:after {
		content: '';
		position: absolute;
		left: 0;
		bottom: -0.2rem;

		width: 100%;
		height: 1px;

		background-color: $black;
		opacity: 0;

		transition: opacity 0.3s;
	}

	&:hover,
	&:focus {
		text-decoration: none;

		&:after {
			opacity: 1;
		}
	}
}

.info__link_wrap {
	@include bp($point_2, min) {
		&.info__link_wrap--style_mod {
			margin-top: 2.8rem;
		}
	}

	@include bp($point_3) {
		margin-top: auto;
	}
}

.info__link {
	position: relative;

	@include text;

	color: $black;

	&:after {
		content: '';
		position: absolute;
		left: 0;
		bottom: -0.2rem;

		width: 100%;
		height: 1px;

		background-color: $black;
		opacity: 0;

		transition: opacity 0.3s;
	}

	&:hover,
	&:focus {
		text-decoration: none;

		&:after {
			opacity: 1;
		}
	}
}
