@import 'ScssHelpers/helpers-all.scss';

.news_search {
	// padding-bottom: 1.1rem;
}

.news_search__wrap {}

.news_search__list {}

.news_search__item {
	@include transition_opacity;

	border-bottom: 1px solid $black;
	max-width: 52rem;

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 1.7rem;
		}
	
		@include bp($point_4 - 1) {
			margin-bottom: 1rem;
		}
	}

	@include bp($point_4, min) {
		padding-bottom: .7rem;
	}

	@include bp($point_4 - 1) {
		padding-bottom: .6rem;
	}

	&:hover {
		& .news_search__link {
			text-decoration: none;
		}
		opacity: .7;
	}
}

.news_search__link {
	@include bp($point_4, min) {
		@include flex_block(row, nowrap, flex-start, flex-end);
	}
}

.news_search__date {
	letter-spacing: .01em;

	color: $black;

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-right: 4.1rem;
		}
	}

	@include bp($point_4, min) {
		font-size: 1.6rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: .7rem;

		font-size: 1.4rem;
	}
}

.news_search__title {
	font-weight: 300;
	font-style: italic;
	font-family: $modern;
	line-height: 1.1;
	letter-spacing: .001em;

	color: $black;

	@include bp($point_4, min) {
		font-size: 2.2rem;
	}

	@include bp($point_4 - 1) {
		margin-left: .3rem;

		font-size: 1.8rem;
	}
}