@import 'ScssHelpers/helpers-all.scss';

.history_main {
}

.history_main__item {
	@include bp($point_2, min) {
		max-width: 80rem;

		&:not(:last-child) {
			margin-bottom: 7rem;
		}

		&:nth-child(9n + 2) {
			max-width: 66rem;
			margin-left: auto;
		}

		&:nth-child(9n + 3) {
			margin-left: 56rem;
		}

		&:nth-child(9n + 4) {
			max-width: 78rem;
		}

		&:nth-child(9n + 5) {
			max-width: 78rem;
			margin-left: 42.4rem;
		}

		&:nth-child(9n + 6) {
			max-width: 66.2rem;
			margin-left: auto;
		}

		&:nth-child(9n + 7) {
			max-width: 78rem;
			min-height: 50rem;
		}

		&:nth-child(9n + 8) {
			max-width: 52rem;
			margin-top: -53rem;
			margin-left: auto;
		}

		&:nth-child(9n + 9) {
			margin: 0 0 12.4rem 14rem;
		}

		&:nth-child(9n + 10),
		&:nth-child(9n + 11) {
			max-width: 94rem;
			margin-left: auto;

			&:not(:last-child) {
				margin-bottom: 12.4rem;
			}
		}
	}

	@include bp($point_3) {
		border-top: 1px solid $black;
		padding: 0.6rem 0;

		&:last-child {
			border-bottom: 1px solid $black;
		}
	}
}

.history_main__year {
	@include h2;

	@include bp($point_2, min) {
		margin-bottom: 1rem;
	}

	@include bp($point_3) {
		position: relative;

		&:before {
			content: '';
			position: absolute;
			top: 50%;
			right: 0;

			width: 1.6rem;
			height: 0.8rem;
			margin-top: -0.4rem;

			background: url('../../images/icons/other-icons/caret.svg') no-repeat center center;
			// background-color: tomato;
			transform-origin: 50% 50%;

			.history_main__item_in--active_state & {
				transform: rotate(-180deg);
			}
		}
	}
}

.history_main__content {
	@include bp($point_3) {
		max-height: 0;
		will-change: height;
		overflow: hidden;
		transition: all 0.3s;
	}
}

.history_main__content_in {
	@include bp($point_3) {
		padding: 2.6rem 0 2rem;
	}
}

.history_main__pic {
	display: block;
	width: 100%;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.8rem;
		}

		@include bp($point_3) {
			margin-bottom: 2rem;
		}
	}
}

.history_main__img {
	display: block;
	width: 100%;
}

.history_main__text {
	@include text;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.6rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.6rem;
		}
	}
}
