.cookie_policy__wrap {
}

.cookie_policy__wrap h1 {
	@include h1;

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 2.2rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 3.8rem;
		}
	}
}

.cookie_policy__wrap hr {
	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: 2.2rem;
		}
	}

	@include bp($point_4 - 1) {
		margin-bottom: 0;
	}
}

.cookie_policy__wrap h2 {
	@include h2;

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 3rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 1.8rem;
		}
	}
}

.cookie_policy__wrap p {
	max-width: 136rem;
	@include text;

	&:not(:last-of-type) {
		@include bp($point_4, min) {
			margin-bottom: 2.8rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 2.6rem;
		}
	}

	@include bp($point_4 - 1) {
		font-size: 1.8rem;
		line-height: 1.44;
	}
}

.cookie_policy__wrap p a {
	color: $black;
	text-decoration: underline;
}

.cookie_policy__wrap p + table {
	margin-top: 5.2rem;
}

.cookie_policy__wrap table {
	width: 100%;

	&:not(:last-child) {
		margin-bottom: 10rem;
	}
}

.cookie_policy__wrap tr {
	border-collapse: collapse;
	border-top: 1px solid $black;
	border-bottom: 1px solid$black;
}

.cookie_policy__wrap td {
	font-weight: 300;

	&:nth-child(1) {
		@include bp($point_4, min) {
			width: 23.2%;
			text-align: center;
		}

		@include bp($point_4 - 1) {
			display: block;
			padding: 1.8rem 0 0.8rem;
		}
	}

	&:nth-child(2) {
		@include bp($point_4, min) {
			width: 76.8%;
			padding-left: 4rem;
		}

		@include bp($point_4 - 1) {
			display: block;
			padding: 0 0 4rem;
		}
	}

	@include bp($point_4, min) {
		padding: 2.2rem 0 2.4rem;

		font-size: 1.6rem;
		letter-spacing: 0.01em;
		line-height: 1;
	}

	@include bp($point_4 - 1) {
		font-size: 1.8rem;
		line-height: 1.44;
	}
}

.cookie_policy__wrap i {
	display: block;

	line-height: 1.4;
	font-weight: 300;
	font-style: italic;

	&:not(:first-child) {
		@include bp($point_4, min) {
			margin-top: 4.4rem;
		}

		@include bp($point_4 - 1) {
			margin-top: 3.6rem;
		}
	}

	@include bp($point_4, min) {
		font-size: 2rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.8rem;
	}
}
