.history_info {
	.rich_text__title {
		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}
}

.history_info__block {
	max-width: 94rem;
	margin-left: auto;
}

.history_info__list {
	&:not(:last-child) {
		margin-bottom: 2.6rem;
	}
}

.history_info__item {
	&:not(:last-child) {
		margin-bottom: 12.6rem;
	}
}
