.btn_v1 {
	@include btn_style_white_border;
	@include btn_size_normal;
}

.btn_v2 {
	@include btn_style_white_border;
	@include btn_size_normal_2;
}

.btn_v3 {
	@include btn_style_white_border;
	@include btn_size_small;
}

.btn_v4 {
	@include btn_style_white_border;
	@include btn_size_small;

	@include bp($point_2, min) {
		padding: 0 3rem;
	}

	@include bp($point_3) {
		font-size: 1.8rem;
		padding: 0 3.4rem;
	}
}

.btn_v5 {
	@include btn_style_white_border;
	@include btn_size_small_2;
}

.btn_icon {
	@include btn_icon;
}

.btn_mail {
	@include btn_mail;
}

.fixed_btn {
	@include btn_style_white_border;
	@include btn_size_normal;
	@include fixed_btn;

	&:not(.fixed_btn--desktop_mod) {
		@include bp($point_2, min) {
			display: none;
		}
	}

	transition: all .3s;

	&.fixed_btn--hidden_mod {
		opacity: 0;
		pointer-events: none;
	}
}

.fixed_btn__icon {
	@include bp($point_2, min) {
		width: 2.2rem;
		height: 2rem;

		&:not(:last-child) {
			margin-right: 1rem;
		}
	}

	@include bp($point_3) {
		width: 1.8rem;
		height: 1.4rem;
	}
}

.fixed_btn__text {
	@include bp($point_3) {
		display: none;
	}
}
