  }
}
@media screen and (max-width: 767px) {
  blockquote p {
    font-size: 1.8rem;
    font-weight: 300;
  }
}

figcaption {
  font-size: 1.4rem;
  font-weight: 300;
  line-height: 1.4;
  letter-spacing: 0.01em;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  height: var(--height-header);
  width: 100%;
  padding: 1rem var(--gl-indent);
  font-size: 1rem;
  will-change: transform;
  z-index: 100;
}
.header:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.16);
  z-index: -1;
}

.header_in {
  display: flex;
  align-items: center;
}

@media screen and (min-width: 1024px) {
  .header_logo {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 22rem;
    height: 2.2rem;
    margin-left: -11rem;
    margin-top: -1.1rem;
  }
}
@media screen and (max-width: 1023px) {
  .header_logo {
    width: 17.2rem;
    height: 1.8rem;
    margin-right: 3rem;
  }
}

.header_logo__img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.nav {
  display: flex;
}
@media screen and (min-width: 1024px) {
  .nav {
    align-items: center;
    flex-grow: 1;
  }
}
@media screen and (max-width: 1023px) {
  .nav {
    position: fixed;
    top: var(--height-header);
    left: 0;
    flex-direction: column;
    width: 100%;
    height: calc(var(--full-screen) - var(--height-header));
    padding: 3.6rem var(--gl-indent);
    background-color: #faf9f5;
    overflow-y: auto;
    transform: translateY(-1rem);
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.3s, transform 0.3s;
    z-index: -3;
  }
  .body--open_menu_state .nav {
    opacity: 1;
    transform: translateY(0);
    pointer-events: initial;
  }
}

@media screen and (min-width: 1024px) {
  .nav__block_wrap {
    display: flex;
    flex-grow: 1;
  }
}
@media screen and (max-width: 1023px) {
  .nav__block_wrap {
    margin-bottom: 4rem;
  }
}

@media screen and (min-width: 1024px) {
  .nav__block {
    display: flex;
    align-items: center;
  }
  .nav__block:first-child {
    margin-right: auto;
  }
}
@media screen and (max-width: 1023px) {
  .nav__block {
    margin-bottom: 0.7rem;
  }
}

@media screen and (min-width: 1024px) {
  .main_menu__list {
    display: flex;
    flex-grow: 1;
    margin-right: 2rem;
  }
}

@media screen and (min-width: 1024px) {
  .main_menu__item:not(:last-child) {
    margin-right: 2.1rem;
  }
}
@media screen and (max-width: 1023px) {
  .main_menu__item:not(:last-child) {
    margin-bottom: 0.7rem;
  }
}

.main_menu__link {
  position: relative;
  display: block;
  color: #000;
  position: relative;
}
@media screen and (min-width: 1024px) {
  .main_menu__link {
    font-size: 1.6rem;
    letter-spacing: 0.16px;
  }
}
@media screen and (max-width: 1023px) {
  .main_menu__link {
    font-size: 2.2rem;
  }
}
.main_menu__link.main_menu__link--submenu_mod {
  user-select: none;
}
@media screen and (max-width: 1023px) {
  .main_menu__link.main_menu__link--submenu_mod:before {
    top: 50%;
    right: 0;
    width: 1.2rem;
    height: 0.6rem;
    margin-top: -0.3rem;
    background-color: tomato;
    background: url("../images/icons/other-icons/caret.svg") no-repeat center center;
    background-size: 100% 100%;
    transform-origin: 50% 50%;
    transition: transform 0.3s;
  }
  .main_menu__item--active_state .main_menu__link.main_menu__link--submenu_mod:before {
    transform: rotate(180deg);
  }
}
.main_menu__link:before {
  content: "";
  position: absolute;
}
@media screen and (min-width: 1024px) {
  .main_menu__link:before {
    top: 100%;
    left: 50%;
    width: calc(100% + 3rem);
    height: 3rem;
    transform: translateX(-50%);
    pointer-events: none;
  }
  .main_menu__item:hover .main_menu__link:before, .main_menu__item:focus .main_menu__link:before {
    pointer-events: initial;
  }
}
@media screen and (min-width: 1024px) {
  .main_menu__link:after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -0.3rem;
    width: 100%;
    height: 1px;
    background-color: #000;
    opacity: 0;
    transition: opacity 0.3s;
  }
  .body--black_mod .main_menu__link:after {
    background-color: #fff;
  }
}
.main_menu__link:hover, .main_menu__link:focus {
  text-decoration: none;
}
.main_menu__link:hover:after, .main_menu__link:focus:after {
  opacity: 1;
}

.header_links {
