@import 'ScssHelpers/helpers-all.scss';

.products_list {
	@include bp($point_2, min) {
		padding-bottom: 3.6rem;
	}
}

.products_list__descr {
	@include text;

	@include bp($point_2, min) {
		margin-bottom: 5rem;
	}

	@include bp($point_3) {
		margin-bottom: 2.6rem;
	}
}

.products_list__picture {
	display: block;
	width: 100%;
	height: 100%;
}

.products_list__img {
	@include cover_img;
}
