@mixin form_el_radio_decor {
	position: absolute;
	top: 0;
	right: 0;

	width: 2rem;
	height: 2rem;

	border-radius: 50%;
	border: 0.1rem solid $black;
	background-color: $white;

	cursor: pointer;

	@include bp($point_11 + 1, min) {
		width: 2rem;
		height: 2rem;
	}

	@include bp($point_11, $point_10 + 1) {
		width: 14px;
		height: 14px;
	}

	@include bp($point_10, $point_9 + 1) {
		width: 18px;
		height: 18px;
	}

	@include bp($point_9, $point_1 + 1) {
		width: 16px;
		height: 16px;
	}

	@include bp($point_1, $point_2 + 1) {
		width: 12px;
		height: 12px;
	}

	@include bp($point_3) {
		width: 2rem;
		height: 2rem;
	}

	&:after {
		@include transition_opacity;

		content: '';
		position: absolute;
		top: 50%;
		left: 50%;

		width: 1rem;
		height: 1rem;

		transform: translate(-50%, -50%);

		border-radius: 100%;
		background-color: $black;
		opacity: 0;

		.form_radio__element:checked + & {
			opacity: 1;
		}

		@include bp($point_11 + 1, min) {
			width: 1rem;
			height: 1rem;
		}
	
		@include bp($point_11, $point_10 + 1) {
			width: 7px;
			height: 7px;
		}
	
		@include bp($point_10, $point_9 + 1) {
			width: 9px;
			height: 9px;
		}
	
		@include bp($point_9, $point_1 + 1) {
			width: 8px;
			height: 8px;
		}
	
		@include bp($point_1, $point_2 + 1) {
			width: 6px;
			height: 6px;
		}
	
		@include bp($point_3) {
			width: 1rem;
			height: 1rem;
		}
	}
}

@mixin contact_form_el_radio {
	position: relative;
	padding-left: 2.6rem;

	font-weight: 300;
	font-family: $font_1;
	color: $black;
	cursor: pointer;

	@include bp($point_2, min) {
		font-size: 1.6rem;
		line-height: 2rem;
	}

	@include bp($point_3) {
		font-size: 1.2rem;
		line-height: 1.5rem;
		letter-spacing: 0.01em;
	}

	.body--black_mod & {
		color: $white;
	}
}

@mixin contact_form_el_radio_decor {
	position: absolute;
	left: 0;

	// width: 1.6rem;
	// height: 1.6rem;

	border-radius: 50%;
	border: 0.1rem solid $black;
	cursor: pointer;

	@include bp($point_11 + 1, min) {
		width: 1.6rem;
		height: 1.6rem;
	}

	@include bp($point_11, $point_1) {
		width: 12px;
		height: 12px;
	}

	@include bp($point_1, $point_2) {
		width: 10px;
		height: 10px;
	}

	@include bp($point_3) {
		width: 1.6rem;
		height: 1.6rem;
	}

	@include bp($point_2, min) {
		top: .1rem;
	}

	@include bp($point_3) {
		top: 0;
		
	}

	&:after {
		@include transition_opacity;

		content: '';
		position: absolute;
		top: 50%;
		left: 50%;

		// width: 0.8rem;
		// height: 0.8rem;

		transform: translate(-50%, -50%);

		border-radius: 100%;
		background: $black;
		opacity: 0;

		.form_radio__element:checked + & {
			opacity: 1;
		}

		@include bp($point_11 + 1, min) {
			width: 0.8rem;
			height: 0.8rem;
		}
	
		@include bp($point_11, $point_1) {
			width: 6px;
			height: 6px;
		}
	
		@include bp($point_1, $point_2 ) {
			width: 5px;
			height: 5px;
		}
	
		@include bp($point_3) {
			width: .8rem;
			height: .8rem;
		}
	}

	.body--black_mod & {
		border: 0.1rem solid $white;
		background-color: $white;
	}
}
