@import 'ScssHelpers/helpers-all.scss';

.contract {
	&.contract--no_mob_indent_mod {
		@include bp($point_3) {
			padding-top: 0;
		}
	}
}

.contract__list_wrap {
	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3.6em;
		}
	}
}

.contract__list {
	@include bp($point_4, min) {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -4rem -4rem 0;
	}
}

.contract__list_item {
	@include bp($point_2, min) {
		width: 33.33%;
	}

	@include bp($point_3, $point_4) {
		width: 50%;
	}

	@include bp($point_4, min) {
		padding: 0 4rem 4rem 0;
	}

	.contract__list_wrap:not(.mobile_slider) & {
		@include bp($point_3) {
			&:not(:last-child) {
				margin-bottom: 4rem;
			}
		}
	}

	.mobile_slider & {
		@include bp($point_4) {
			width: 25.6rem;
			padding: 0;

			&:not(:last-child) {
				margin-right: 2rem;
			}
		}
	}
}

.contract_item {
	display: block;
	color: $black;

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

.contract_item__pic {
	position: relative;
	display: block;
	overflow: hidden;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2rem;
		}

		@include bp($point_3) {
			margin-bottom: 1rem;
		}
	}

	.projects_section & {
		@include bp($point_2, min) {
			&:not(:last-child) {
				margin-bottom: 1.2rem;
			}
		}

		@include bp($point_3) {
			&:not(:last-child) {
				margin-bottom: 1rem;
			}
		}
	}

	&:before {
		content: '';
		display: block;
		@include bp($point_2, min) {
			padding-bottom: 62.4%;
		}

		@include bp($point_3) {
			padding-bottom: 66.4%;
		}
	}
}

.contract_item__img {
	@include cover_img;
	@include hover_img('.contract_item');

	position: absolute;
	top: 0;
	left: 0;
}

.contract_item__title {
	@include text_2;
}

.contract__btn {
	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3) {
		@include flex_block(row, nowrap, center, center);
	}
}
