@import 'ScssHelpers/helpers-all.scss';

.store_list_item {
	min-height: 8rem;

	border-bottom: 1px solid $black;

	@include flex_block(row, wrap, flex-start, flex-start, flex-start);

	@include bp($point_2, min) {
		padding: 1.1rem 0;
	}

	@include bp($point_3) {
		padding: 1.7rem 0 1.1rem;
	}

	.store__block_list_item:first-child & {
		border-top: 1px solid $black;
	}

	.store--contacts_mod & {
		min-height: 6rem;

		@include bp($point_2, min) {
			padding: 1.5rem 0;
		}

		@include bp($point_3) {
			padding: 1.7rem 0 1.1rem;
		}
	}
}

.store_list_item__name,
.store_list_item__address {
	font-family: $modern;
	font-weight: 300;

	@include bp($point_2, min) {
		font-size: 2.2rem;
	}

	@include bp($point_3) {
		font-size: 1.8rem;
		line-height: 1.44;
	}
}

.store_list_item__name,
.store_list_item__address,
.store_list_item__phone,
.store_list_item__email {
	@include bp($point_2, min) {
		padding-right: 5rem;
	}

	a {
		position: relative;
		color: $black;

		&:after {
			content: '';
			position: absolute;
			left: 0;
			bottom: -0.2rem;

			width: 100%;
			height: 1px;

			background-color: $black;
			opacity: 0;

			transition: opacity 0.3s;
		}

		&:hover,
		&:focus {
			text-decoration: none;

			&:after {
				opacity: 1;
			}
		}
	}
}

.store_list_item__name {
	.store--contacts_mod & {
		@include bp($point_4, min) {
			width: 59.8%;
		}
	
		@include bp($point_4 - 1) {
			width: 100%;
		}

		span {
			@include bp($point_4 - 1) {
				display: inline-block;

				max-width: 17.5rem;
				width: 100%;
			}
		}
	}

	@include bp($point_2, min) {
		width: 23.5%;
	}

	@include bp($point_3) {
		width: 100%;
		margin-bottom: 2.6rem;
	}
}

.store_list_item__address {
	@include bp($point_2, min) {
		width: 33.4%;
	}

	@include bp($point_3) {
		width: 100%;
		margin-bottom: 2.6rem;
	}
}

.store_list_item__phone,
.store_list_item__email,
.store_list_item__category {
	@include bp($point_2, min) {
		padding-top: 0.4rem;
		font-size: 1.6rem;
	}

	@include bp($point_3) {
		font-size: 1.4rem;
		letter-spacing: 0.01em;
	}
}

.store_list_item__phone {
	.store--contacts_mod & {
		@include bp($point_2, min) {
			width: 25%;
		}

		@include bp($point_4 - 1) {
			width: 50%;
		}
	}

	@include bp($point_2, min) {
		width: 14.7%;
	}

	@include bp($point_3) {
		width: 100%;
		margin-bottom: 0.4rem;
	}
}

.store_list_item__email {
	.store--contacts_mod & {
		@include bp($point_2, min) {
			width: 15%;
		}

		@include bp($point_4 - 1) {
			width: 50%;
			text-align: right;
		}
	}
	@include bp($point_2, min) {
		width: 20%;
	}

	@include bp($point_3) {
		width: 60%;
	}
}

.store_list_item__category {
	margin-left: auto;
}
