@import 'ScssHelpers/helpers-all.scss';

.popup {
	@include popup;
}

.popup__in {
	position: relative;

	max-width: 100.6rem;
	width: 100%;

	
	background-color: $white;
	
	@include bp($point_2, min) {
		margin: auto;
		padding: 4.5rem 3.4rem;
		border-radius: 1.3rem;
	}
	
	@include bp($point_3) {
		margin: auto auto 0 auto;
		border-radius: 1.3rem 1.3rem 0 0;
		padding: 4.8rem 2.2rem 3rem 2.2rem;
	}
}

.popup__close {
	position: absolute;

	display: block;
	width: 2rem;
	height: 2rem;

	cursor: pointer;

	@include bp($point_2, min) {
		top: 5.4rem;
		right: 3.4rem;
	}

	@include bp($point_3) {
		top: 2.4rem;
		right: 2rem;

		.popup__title--filter_mod & {
			top: -2.4rem;
			right: -0.2rem;
		}
	}

	&::before,
	&::after {
		content: '';
		position: absolute;
		top: 0.9rem;
		left: -0.3rem;

		width: 2.6rem;
		height: 0.2rem;

		background-color: $black;
		border-radius: 1.2rem;

		transition: opacity 0.3s ease;
	}

	&::before {
		transform: rotate(45deg);
	}

	&::after {
		transform: rotate(-45deg);
	}

	&:hover {
		opacity: 0.7;
	}
}

.popup__head {
	padding-right: 2.5rem;

	.popup--form_mod &:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3.5rem;
		}
		@include bp($point_3) {
			margin-bottom: 2rem;
		}
	}

	&:not(:last-child) {

		@include bp($point_2, min) {
			margin-bottom: 4.5rem;
		}

		@include bp($point_3) {
			margin-bottom: 2.8rem;
		}
	}

	.popup__title--filter_mod & {
		position: relative;

		@include bp($point_3) {
			margin-bottom: 4rem;
		}

		&::before {
			content: '';
			position: absolute;
			bottom: -1rem;
			left: 0;
			height: 1px;
			width: 100%;
			background-color: currentColor;
		}
	}
}

.popup__title {
	@include h2;

	@include bp($point_3) {
		line-height: 3rem;
		max-width: 27rem;

		// border-bottom: 1px solid $black;
	}
}

.popup__btn_block {
	@include flex_block(row, nowrap, center, stretch);
	&:not(:last-child) {
		margin: 2rem 0 5.5rem;
	}
}

.popup__text_content {
	@include text;
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 6.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 2.6rem;
		}
	}

	p:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 2.2rem;
		}
	}
}

.popup__radio_wrap {
	@include bp($point_3) {
		margin-bottom: 3rem;
	}
}

.popup__share_content {
	@include flex_block(row, wrap, flex-start, center);
}

.popup__share_img_w {
	display: block;
	@include bp($point_2, min) {
		width: 27.4rem;
		height: auto;
		max-height: 17rem;
		margin-right: 6.2rem;
	}

	@include bp($point_3) {
		width: 100%;
		height: auto;
		margin-bottom: 3rem;
	}
}

.popup__share_img {
	@include contain_img;
}

.popup__share_btns_list {
	@include flex_block(row, wrap, flex-start, stretch);

	@include bp($point_2, min) {
		margin: 0 -4.6rem -3rem 0;
		max-width: 58rem;
	}

	@include bp($point_3) {
		width: 100%;
	}
}

.popup__share_btns_item {
	@include bp($point_2, min) {
		padding: 0 4.6rem 3rem 0;
	}

	@include bp($point_3) {
		width: 100%;
		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}
}

.popup__share_btn {
	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, center);
		min-width: 24rem;
		padding-left: 4.6rem;
	}

	@include bp($point_3) {
		@include flex_block(row, nowrap, center, center);
		width: 100%;
	}
}

.popup__share_btn_text {
	display: block;
	color: $black;
	font-weight: 500;
	font-family: $modern;
	letter-spacing: 0.01em;

	@include bp($point_2, min) {
		font-size: 1.6rem;
	}

	@include bp($point_3) {
		min-width: 7rem;
		font-size: 1.4rem;
		text-align: left;
	}
}

.popup__share_btn_icon {
	width: 2rem;
	height: 2rem;

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-right: 2rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-right: 1rem;
		}
	}
}
