@import 'ScssHelpers/helpers-all.scss';

.information {
	@include bp($point_2, min) {
		z-index: 2;

		padding-top: 0;
		padding-bottom: 0;
		margin-bottom: -4.4rem;
	}

	@include bp($point_3) {
		padding-bottom: 0;
	}
}

.information__accordion_item_body {
	max-height: 0;
	opacity: 0;

	transition: all 0.25s ease-out;
	will-change: max-height;
	overflow: hidden;
	pointer-events: none;

	.information__accordion--active-mod & {
		opacity: 1;
		pointer-events: auto;
	}
}

// ----------------------------------------------------------

//---------------------------------------------accordion
.icon {
	&.size-mod {
		display: block;
		width: 100%;
		height: 100%;
	}
}

.information__accordion_item_head {
	@include flex_block(row, wrap, flex-start, center, flex-start);

	font-size: 1.6rem;
	font-weight: 700;

	background-repeat: no-repeat;
	background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACAAAAAZBAMAAACiITGiAAAAAXNSR0IB2cksfwAAACFQTFRFAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAbdFIwQAAAAt0Uk5TAEzvbCvOjQqurfTFWbgxAAAAN0lEQVR4nGNgGNRAAEQwIviMyiDSSAAh4prAwMAWgqSFvbMAiJANYQsBKUIGVovR7GEUYBjkAABpZAVkNByEwAAAAABJRU5ErkJggg==');
	background-position: right center;
	cursor: pointer;

	transition: border-color 0.3s;

	@include transition_color;

	@include bp($point_2, min) {
		padding: 2.3rem 0;
	}

	@include bp($point_3) {
		padding: 1rem 0;
	}

	.information__accordion--active-mod & {
		background-image: url('data:image/png;base64,');
	}

	.information__accordion_item_title {
		padding-right: 2rem;
		@include h2;
	}

	.information__accordion_item_icon_w {
		display: block;
		width: 1.8rem;
		height: 0.8rem;

		margin: 0 0 0 auto;

		transform-origin: center;
		transition: 0.3s;
		transform: rotateZ(0deg);

		.information__accordion--active-mod & {
			transform: rotateZ(180deg);
		}
	}

	&:hover,
	&:focus {
		color: lighten($black, 30%);

		.information__accordion_item_icon_w {
			color: lighten($black, 30%);
		}
	}

	.information__accordion_item:not(:last-child) & {
		border-bottom: 1px solid gray;
	}

	.information__accordion--active-mod:first-child & {
		border-bottom: none;
		border-top: 1px solid gray;
	}

	.information__accordion_item:first-child & {
		border-top: 1px solid gray;
	}
}

.information__accordion_item_body {
	font-size: 1.6rem;
}

.information__accordion_item_content {
	@include bp($point_2, min) {
		padding: 0.7rem 0 10rem;
	}

	@include bp($point_3) {
		padding: 1.7rem 0 4.4rem;
	}

	.information__accordion_item:last-child & {
		@include bp($point_2, min) {
			padding-bottom: 11.8rem;
		}

		@include bp($point_3) {
			padding-bottom: 2.8rem;
		}
	}

	.information__accordion_item:not(:last-child) & {
		border-bottom: 1px solid gray;
	}
}

//--------------------------- information block

.information__block {
	@include flex_block(row, wrap, flex-start, flex-start, flex-start);
}

.information__block_info {
	@include text;

	@include bp($point_2, min) {
		width: 43%;
		padding-right: 13rem;
	}

	@include bp($point_3) {
		width: 100%;
		margin-bottom: 2.6rem;
	}

	p {
		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 4rem;
			}

			@include bp($point_3) {
				margin-bottom: 2.2rem;
			}
		}
	}
}

.information__block_images {
	@include bp($point_2, min) {
		width: 57%;

		margin: 0 -1rem -1rem 0;

		@include flex_block(row, wrap, flex-start, flex-start, flex-start);
	}

	@include bp($point_3) {
		width: 100%;
	}
}

.information__block_images_col {
	@include bp($point_2, min) {
		width: 50%;
		padding: 0 1rem 1rem 0;
	}

	@include bp($point_3) {
		width: 100%;

		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.information__block_images_text {
	display: block;

	@include bp($point_2, min) {
		margin-bottom: 2rem;
	}

	@include bp($point_3) {
		margin-bottom: 0.6rem;
		font-size: 1.4rem;
	}
}

.information__block_images_picture {
	display: block;
	width: 100%;
	height: 100%;
}

.information__block_images_img {
	@include cover_img;
}
