@import 'ScssHelpers/helpers-all.scss';

.award {
}

.award__wrap {
	max-width: 80rem;
	width: 100%;
	margin: 0 auto;
}

.award__list {
	border-top: 1px solid $black;

	@include bp($point_4, min) {
		padding-top: 1.6rem;

		&:not(:last-child) {
			margin-bottom: 2.8rem;
		}
	}

	@include bp($point_4 - 1) {
		padding-top: 2rem;

		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.award__item {
	@include flex_block(row, nowrap, flex-start, center);

	border-bottom: 1px solid $black;

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 1.8rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 2rem;
		}
	}

	@include bp($point_4, min) {
		padding-bottom: 1.6rem;
	}

	@include bp($point_4 - 1) {
		padding-bottom: 1.8rem;
	}
}

.award__item_body {
	width: 75%;
	flex-shrink: 0;

	padding-right: 2.4rem;

	@include bp($point_4, min) {
		@include flex_block(row, wrap, flex-start, center);
	}
}

.award__item_prize {
	display: block;

	font-weight: 300;
	line-height: 1.25;

	font-family: $modern;

	@include bp($point_4, min) {
		width: 50%;
		flex-shrink: 0;
		padding-right: 1.4rem;

		font-size: 2.2rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.8rem;

		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}
}

.award__item_prize,
.award__item_name {
	color: $black;
}

.award__item_name,
.award__item_year {
	@include text_2;
}

.award__item_name {
	display: block;

	@include bp($point_4, min) {
		width: 50%;
		flex-shrink: 0;

		text-align: right;
	}
}

.award__item_year {
	width: 25%;
	flex-shrink: 0;

	text-align: right;
}

.award__btn {
	@include flex_block(row, nowrap, center, center);
}
