@import 'ScssHelpers/helpers-all.scss';

.collection {
}

.collection__head {
	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: 7rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 6rem;
		}
	}
}

