.form_radio {
	&:not(.form_radio--mod_1) {
		border-bottom: 1px solid $black;

		@include flex_block(row, wrap, flex-start, flex-start, center);

		@include bp($point_2, min) {
			min-width: 24rem;
			padding: 1rem 0;

			&:not(:last-child) {
				margin-right: 4rem;
			}
		}

		@include bp($point_3) {
			width: 100%;

			padding: 1rem 0;

			&:not(:last-child) {
				margin-bottom: 2rem;
			}
		}
	}
}

.form_radio__block {
	position: relative;

	display: block;
	width: 100%;

	.contact_form & {
		display: flex;
		align-items: flex-start;
	}
}

.form_radio__element {
	display: none;
}

.form_radio__label {
	@include form_el_radio;
}

.form_radio__label_decor {
	@include form_el_radio_decor;
}

.contact_form__radio_label {
	@include contact_form_el_radio;
}

.contact_form__radio_label_decor {
	@include contact_form_el_radio_decor;
}
