@import 'ScssHelpers/helpers-all.scss';

.download_area__accordion {
	border-top: 1px solid $black;
}

.download_area__accordion_item_head {
	@include flex_block(row, wrap, flex-start, center, flex-start);
	
	color: $black;
	
	background-repeat: no-repeat;
	background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACAAAAAZBAMAAACiITGiAAAAAXNSR0IB2cksfwAAACFQTFRFAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAbdFIwQAAAAt0Uk5TAEzvbCvOjQqurfTFWbgxAAAAN0lEQVR4nGNgGNRAAEQwIviMyiDSSAAh4prAwMAWgqSFvbMAiJANYQsBKUIGVovR7GEUYBjkAABpZAVkNByEwAAAAABJRU5ErkJggg==');
	background-position: right center;
	cursor: pointer;
	transition: border-color 0.3s;
	
	@include transition_color;
	
	@include bp($point_2, min) {
		padding: 2.3rem 0;
	}

	@include bp($point_3) {
		padding: 1rem 0 1rem;
	}

	&:hover,
	&:focus {
		color: lighten($black, 30%);
	}

	.download_area__accordion--active_mod & {
		background-image: url('data:image/png;base64,');
	} 
}

.download_area__accordion_item_body {
	max-height: 0;
	opacity: 0;

	transition: all 0.25s ease-out;
	will-change: max-height;
	pointer-events: none;
	
	@include bp($point_2, min) {
		overflow: hidden; // for design purpose
	}

	.download_area__accordion--active_mod & {
		opacity: 1;
		pointer-events: auto;
	}
}

.download_area__accordion_item_content {
	@include bp($point_2, min) {
		padding: .4rem 0 10rem;
	}

	@include bp($point_3) {
		padding: .6rem 0 2.6rem;
	}
}

.download_area__accordion_item_title {
	padding-right: 2rem;
	@include h2;
}

.download_area__accordion_item_icon {
	display: block;
	width: 1.8rem;
	height: 0.8rem;

	margin: 0 0 0 auto;

	transform-origin: center;
	transition: transform .3s ease;
	transform: rotateZ(0deg);

	.download_area__accordion--active_mod & {
		transform: rotateZ(180deg);
	}

	.download_area__accordion_item_head:hover & {
		color: lighten($black, 30%);
	}
}

.download_area__accordion_item {
	border-bottom: 1px solid $black;
}

.download_area__catalog_list {
	
	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, stretch);
		margin: 0 -4.2rem -4.2rem 0;
	}
	
	@include bp($point_3) {
		padding: 2.2rem 0 0 0;
		margin: 0;
	}
}

.download_area__catalog_item_w {
	
	@include bp($point_2, min) {
		padding: 0 4.2rem 4.2rem 0;
		width: 25%;
	}
	
	@include bp($point_3) {
		padding: 0;
		width: 25.4rem;
		&:not(:last-child) {
			margin-right: 2.4rem;
		}
	}
}

.download_area__catalog_item {
	@include flex_block(column, nowrap, flex-start, flex-start);

	&:hover {
		text-decoration: none;
	}
}

.download_area__catalog_img_w {
	position: relative;
	display: block;
	width: 100%;
	padding-bottom: 131.3%;
	overflow: hidden;
	
	@include bp($point_2, min) {
		margin-bottom: 2rem;
	}

	@include bp($point_3) {
		margin-bottom: 1rem;
	}
}

.download_area__catalog_img {
	@include cover_img;
	@include hover_img('.download_area__catalog_item');
	position: absolute;
	top: 0;
	left: 0;

}

.download_area__catalog_title {
	@include text_2;
	color: $black;
	text-decoration: none;
}

.download_area__files_list {
	@include flex_block(row, wrap, flex-start, stretch);
	margin-bottom: 3rem;
	
	@include bp($point_5, min) {
		margin-right: -4rem;
	}

	@include bp($point_5 - 1) {
		margin-right: 0;
	}
}

.download_area__files_item_w {
	@include bp($point_5, min) {
		padding-right: 4rem;
		width: 50%;
	}
	
	@include bp($point_5 - 1) {
		padding-right: 0;
		width: 100%;
	}

	.download_area__files_list--3_col_mod & {
		@include bp($point_2, min) {
			width: 33.33%;
		}

		@include bp($point_3, $point_5) {
			width: 50%;
		}

		@include bp($point_5 - 1) {
			width: 100%;
		}
	}
}

.download_area__files_item {
	@include flex_block(row, nowrap, flex-start, center);
	padding: 1rem 0;
	color: $black;
	border-bottom: 1px solid $black;
	transition: color .3s ease;
	&:hover,
	&:focus {
		text-decoration: none;
		color: rgba($black, .7)
	}
}

.download_area__files_item_text {
	@include text_2;
	display: block;
	width: 100%;
	color: currentColor;

}

.download_area__files_item_icon {
	width: 1.6rem;
	height: 2.1rem;
	color: currentColor;
}

.download_area__files_btn_w {
	@include bp($point_5, min) {
		display: none;	
	}

	@include bp($point_5 - 1) {
		@include flex_block(row, nowrap, center, center);
	}
}