@import 'ScssHelpers/helpers-all.scss';

.collections_section__list {
	display: flex;
	flex-wrap: wrap;
	&,
	&.swiper {
		@include bp($point_2, min) {
			margin-right: -18.2rem;
		}
	}

	@include bp($point_3) {
		:not(.mobile_slider) & {
			margin: 0 -3.6rem -3.6rem 0;
		}

		.mobile_slider & {
			margin: 0;
		}
	}
}

.collections {
	will-change: opacity;

	@include bp($point_2, min) {
		&,
		&.swiper-slide {
			padding-right: 18.2rem;

			width: 30.8%;

			&:nth-child(6n + 2),
			&:nth-child(6n + 4) {
				width: 38.4%;
			}

			&:nth-child(6n + 2) {
				margin-top: 18.4rem;
			}

			&:nth-child(6n + 3) {
				margin-top: 31.4rem;
			}

			&:nth-child(6n + 4) {
				margin-top: 9rem;

				.collections_section--img_mod & {
					margin-top: 19.6rem;
				}
			}

			&:nth-child(6n + 5) {
				margin-top: -5.4rem;
			}

			&:nth-child(6n + 6) {
				margin-top: 17.4rem;
			}
		}
	}

	:not(.mobile_slider) & {
		@include bp($point_3) {
			padding: 0 3.6rem 3.6rem 0;
		}

		@include bp($point_3, $point_4) {
			&:nth-child(4n + 1),
			&:nth-child(4n + 4) {
				width: 40%;
			}

			&:nth-child(4n + 2),
			&:nth-child(4n + 3) {
				width: 60%;
			}
		}

		@include bp($point_4 - 1) {
			width: 100%;
		}
	}

	.mobile_slider & {
		@include bp($point_3) {
			width: auto !important;
			max-width: calc(100vw - var(--gl-indent) * 2);
			padding: 0;
			&:not(:last-child) {
				margin-right: 2rem;
			}
		}
	}
}

.collections_main {
	display: block;
	color: $black;

	&:hover,
	&:focus {
		text-decoration: none;
	}

	@include bp($point_3) {
		transform: none !important;
	}
}

.collections__pic_wrap {
	overflow: hidden;
}

.collections__pic {
	position: relative;
	display: block;
	margin-bottom: 1rem;

	overflow: hidden;
	will-change: transform, opacity;

	@include bp($point_2, min) {
		&:before {
			content: '';
			display: block;
			padding-bottom: 125%;

			.collections:nth-child(6n + 2) & {
				padding-bottom: 73.5%;
			}

			.collections:nth-child(6n + 4) & {
				padding-bottom: 95%;

				.collections_section--img_mod & {
					padding-bottom: 74%;
				}
			}

			.collections:nth-child(6n + 5) & {
				padding-bottom: 130.7%;
			}

			.collections:nth-child(6n + 6) & {
				padding-bottom: 124%;
			}
		}
	}

	:not(.mobile_slider) & {
		@include bp($point_3, $point_4) {
			height: 31.8rem;
		}

		@include bp($point_4 - 1) {
			height: auto;
		}
	}

	.mobile_slider & {
		@include bp($point_3) {
			height: 31.8rem;
		}
	}
}

.collections__img {
	@include cover_img;
	@include hover_img('.collections_main');

	@include bp($point_2, min) {
		position: absolute;
		top: 0;
		left: 0;
	}
}

.collections__title {
	@include text_2;
}
