@import 'ScssHelpers/helpers-all.scss';

.press_section {
	&.press_section--no_indent_mod {
		padding-top: 0;
	}
}

.press__list_wrap {
	.mobile_slider__controls_item {
		margin-top: -4rem;
	}

	@include bp($point_2, min) {
		margin-bottom: 7rem;
	}

	@include bp($point_3) {
		&:not(.press__list_wrap--slider_mod) {
			margin-bottom: 4rem;
		}
	}
}

.press__list {
	display: flex;
	flex-wrap: wrap;
	&,
	&.swiper {
		@include bp($point_2, min) {
			margin: 0 -4.5rem -5rem 0;
		}
	}

	.press__list_wrap:not(.press__list_wrap--slider_mod) & {
		margin: 0 -2rem -3.4rem 0;
	}
}

.press {
	&,
	&.swiper-slide {
		@include bp($point_2, min) {
			width: 25%;
			padding: 0 4.5rem 5rem 0;
		}
	}

	.press__list_wrap:not(.press__list_wrap--slider_mod) & {
		@include bp($point_2, min) {
			width: 25%;
			padding: 0 4.5rem 5rem 0;
		}
		@include bp($point_3) {
			width: 50%;
			padding: 0 2rem 3.4rem 0;
		}
	}

	&.swiper-slide {
		@include bp($point_3) {
			width: 25.6rem;

			&:not(:last-child) {
				margin-right: 2rem;
			}
		}
	}
}

.press__link {
	display: block;

	color: $black;

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

.press__pic {
	position: relative;
	display: block;
	overflow: hidden;

	@include bp($point_2, min) {
		margin-bottom: 2.2rem;
	}

	@include bp($point_3) {
		margin-bottom: 0.8rem;
	}

	&:before {
		content: '';
		display: block;
		padding-bottom: 140.2%;
	}
}

.press__img {
	@include cover_img;
	@include hover_img('.press__link');

	position: absolute;
	top: 0;
	left: 0;
}

.press__title {
	@include text_2;
}

.press__subtitle {
	@include text_2;
}

.press__btn_wrap {
	display: flex;
	justify-content: center;
}
