@import 'ScssHelpers/helpers-all.scss';

.about_section {
}

.about_section__wrap {
	@include bp($point_4, min) {
		@include flex_block(row, nowrap, flex-start, flex-start);
	}
}

.about_section__text {
	max-width: 107rem;
	width: 100%;

	@include text;

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-right: 3rem;
		}

		p {
			&:not(:last-child) {
				margin-bottom: 2.8rem;
			}
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 2.8rem;
		}

		p {
			&:not(:last-child) {
				margin-bottom: 2.2rem;
			}
		}
	}
}

.about_section__img_in {
	position: relative;
	display: block;

	&::after {
		content: '';
		display: block;
		padding-bottom: 100%;
	}

	@include bp($point_2, min) {
		width: 38rem;
	}

	@include bp($point_3, $point_4) {
		width: 28rem;
	}

	@include bp($point_4, min) {
		flex-shrink: 0;
		margin-left: auto;
	}

	@include bp($point_4 - 1) {
		max-width: 35rem;
		width: 100%;
		margin: 0 auto;
	}
}

.about_section__img {
	position: absolute;
	top: 0;
	left: 0;

	@include cover_img;
}
