@import 'ScssHelpers/helpers-all.scss';

.single_section {
}

.single_section__list {
}

.single_section__item {
	border-bottom: none;
	border-top: 1px solid gray;

	&:last-child {
		border-bottom: 1px solid gray;

		transition: border-color 0.3s;

		&.single_section--active-mod {
			border-bottom: 1px solid transparent;
		}
	}
}

.icon {
	&.size-mod {
		display: block;
		width: 100%;
		height: 100%;
	}
}

.single_section__item_head {
	@include flex_block(row, wrap, flex-start, center, flex-start);

	font-size: 1.6rem;
	font-weight: 700;
	cursor: pointer;

	transition: border-color 0.3s;

	@include transition_color;

	@include bp($point_2, min) {
		padding: 2.3rem 0;
	}

	@include bp($point_3) {
		padding: 0.6rem 0;
	}

	.single_section__item_title {
		padding-right: 2rem;
		@include h2;
	}

	.single_section__item_icon_w {
		display: block;
		width: 1.6rem;
		height: 0.8rem;

		margin: 0 0 0 auto;

		transform-origin: center;
		transition: 0.3s;
		transform: rotateZ(0deg);

		.single_section--active-mod & {
			transform: rotateZ(180deg);
		}
	}

	&:hover,
	&:focus {
		color: lighten($black, 30%);

		.single_section__item_icon_w {
			color: lighten($black, 30%);
		}
	}
}

.single_section__item_body {
	max-height: 0;
	opacity: 0;

	transition: all 0.25s ease-out;
	will-change: max-height;
	overflow: hidden;
	pointer-events: none;

	.single_section--active-mod & {
		opacity: 1;
		pointer-events: auto;
	}
}

.single_section__item_content {
	padding-top: 0.8rem;

	@include bp($point_2, min) {
		padding-bottom: 9.4rem;
	}

	@include bp($point_3) {
		padding-bottom: 3rem;
	}
}
