@import 'ScssHelpers/helpers-all.scss';

.slider {
	position: relative;
	overflow: hidden;
}

.slider__controls {
}

.slider__btn {
	position: absolute;
	z-index: 10;

	display: block;
	color: $white;

	.description--width_mod & {
		color: $black;
	}

	cursor: pointer;

	transition: color 0.3s ease;
	transform: translateY(-50%);

	&.slider__btn--prev_mod {
		@include bp($point_2, min) {
			left: 7.2rem;
		}

		@include bp($point_3) {
			left: 1rem;
		}
	}

	&.slider__btn--next_mod {
		@include bp($point_2, min) {
			right: 7.2rem;
		}

		@include bp($point_3) {
			right: 1rem;
		}
	}

	&:hover,
	&:focus {
		color: $black;

		.description--width_mod & {
			color: $gray_2;
		}
	}

	&.swiper-button-disabled {
		opacity: 0.5;
		pointer-events: none;
	}

	@include bp($point_2, min) {
		width: 3.4rem;
		height: 5rem;
		top: calc(50% - 1.2rem);
	}

	@include bp($point_3) {
		width: 1.6rem;
		height: 3rem;
		top: calc(50% - 1.4rem);
	}
}

.slider__btn_icon {
}

.slider__pagination {
	position: absolute;
	right: 0;
	z-index: 10;
	bottom: 0;

	@include text_2;

	.description--width_mod & {
		@include bp($point_4, min) {
			right: 50%;
			transform: translateX(35rem);
		}
	}
}

.slider__slider {
	position: relative;
}

.slider__list {
}

.slider__item {
}

.slider__item_img_in {
	position: relative;
	display: block;
	width: 100%;

	.description--width_mod & {
		max-width: 70.4rem;
		margin: 0 auto;
	}

	&:not(:last-child) {
		margin-bottom: 1rem;
	}

	@include bp($point_4, min) {
		padding-bottom: 53.4%;
	}

	@include bp($point_4 - 1) {
		padding-bottom: 62.4%;

		.description--width_mod & {
			padding-bottom: 124%;
		}
	}
}

.slider__item_pic {
	@include stretch;
}

.slider__item_img {
	@include cover_img;
}

.slider__img_in {
	padding-right: 4rem;

	.description--width_mod & {
		max-width: 70rem;
		margin: 0 auto;
	}
}

.slider__img_name {
	position: relative;

	@include text_2;

	color: $black;

	&:after {
		content: '';
		position: absolute;
		left: 0;
		bottom: 0;

		width: 100%;
		height: 1px;

		background-color: $black;
		opacity: 0;

		transition: opacity 0.3s;
	}

	&:hover,
	&:focus {
		text-decoration: none;

		&:after {
			opacity: 1;
		}
	}
}
