@import 'ScssHelpers/helpers-all.scss';

.section_head {
	border-top: 1px solid $black;

	@include flex_block(row, wrap, flex-start, center, flex-start);

	.body--black_mod & {
		border-top: 1px solid $white;
	}

	@include bp($point_2, min) {
		padding-top: 1.4rem;
	}

	@include bp($point_3) {
		padding-top: 1.2rem;
	}

	&.section_head--sm_mod {
		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 3rem;
				padding-top: 1.8rem;
			}
		}
	}

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 5rem;
		}

		&.section_head--offset_mod {
			&:not(:last-child) {
				margin-bottom: 2rem;
			}
		}

		.recent_projects & {
			&:not(:last-child) {
				margin-bottom: 5.6rem;
			}
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.section_head__title,
.section_head__title_link {
	@include h1;

	&:not(:last-child) {
		margin-right: 1rem;
	}

	.section_head--font_mod &,
	.section_head--sm_mod & {
		@include bp($point_2, min) {
			font-size: 4rem;
		}

		@include bp($point_3) {
			font-size: 2.4rem;
		}
	}
}

.section_head__title_link {
	color: $black;

	transition: color 0.3s ease;

	&:hover,
	&:focus {
		text-decoration: none;
		// color: $color_link_base;
	}
}

.section_head__link {
	@include text_2;
	@include hover_link;

	margin-left: auto;

	&:after {
		bottom: -0.6rem !important;
	}
	color: $black;

	@include bp($point_3) {
		margin-top: 0.4rem;
	}
}

.section_head__subtitle {
	@include text_2;

	margin-left: auto;
}
