.hero_v4 {
}

.hero_v4__title {
	@include h1;

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: 2.2rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}
}

.hero_v4__wrap {
	@include bp($point_4, min) {
		@include flex_block(row, wrap, flex-start, flex-start);

		&:not(:last-child) {
			margin-bottom: 10.4rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 5.8rem;
		}
	}
}

.hero_v4__col {
	@include bp($point_4, min) {
		flex-shrink: 0;

		&:not(:last-child) {
			padding-right: 2.2rem;
		}

		&:nth-child(2n + 1) {
			width: 52%;
		}
		&:nth-child(2n + 2) {
			width: 48%;
			padding-top: 2rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.hero_v4__text {
	font-weight: 300;
	line-height: 1.36;

	@include bp($point_2, min) {
		font-size: 2.2rem;
		font-family: $modern;

		&:not(:last-child) {
			margin-bottom: 13rem;
		}
	}

	@include bp($point_3) {
		font-size: 1.6rem;

		&:not(:last-child) {
			margin-bottom: 2.8rem;
		}
	}
}

.hero_v4__pic {
	position: relative;
	display: block;

	.hero_v4__col:nth-child(2n + 1) & {
		@include bp($point_4, min) {
			padding-bottom: 62.8%;
		}

		@include bp($point_4 - 1) {
			&::after {
				content: '';
				display: block;
				padding-bottom: 100%;
			}
		}
	}

	.hero_v4__col:nth-child(2n + 2) & {
		@include bp(1440px, min) {
			margin-right: -12.4rem;
		}

		@include bp($point_4, min) {
			width: 78.8%;
			margin-left: auto;
			padding-bottom: 79%;
		}

		@include bp($point_4 - 1) {
			&::after {
				content: '';
				display: block;
				padding-bottom: 66.66%;
			}
		}
	}

	@include bp($point_4, min) {
		width: 94%;
	}

	@include bp($point_4 - 1) {
		width: 100%;
		margin: 0 auto;
	}
}

.hero_v4__img {
	position: absolute;
	top: 0;
	left: 0;

	@include cover_img;
}

.hero_v4__description {
	max-width: 152rem;

	@include text;

	@include bp($point_4, min) {
		p {
			&:not(:last-child) {
				margin-bottom: 2.8rem;
			}
		}

		&:not(:last-child) {
			margin-bottom: 10.2rem;
		}
	}

	@include bp($point_4 - 1) {
		p {
			&:not(:last-child) {
				margin-bottom: 2.2rem;
			}
		}

		&:not(:last-child) {
			margin-bottom: 5.6rem;
		}
	}
}

.hero_v4__bottom {
	margin: 0 auto;

	@include bp($point_4, min) {
		width: 66%;
	}
}

.hero_v4__video {
	position: relative;

	// padding-bottom: calc(660 / 1080 * 100%);

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: 7.4rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 5.8rem;
		}
	}
}

.hero_v4__video_wrap {
	// position: absolute;
	// top: 0;
	// left: 0;

	// width: 100%;
	// height: 100%;
	// // @include cover_img;
}

.hero_v4__video_poster {
	@include cover_img;
}

.hero_v4__video_trigger {
	@include transition_color;

	position: absolute;
	left: 50%;
	top: 50%;

	color: $white;

	transform: translate(-50%, -50%);

	@include bp($point_2, min) {
		width: 8.8rem;
		height: 10.2rem;
	}

	@include bp($point_3) {
		width: 2.8rem;
		height: 3.2rem;
	}

	&:hover,
	&:focus {
		color: $gray;
	}
}

.hero_v4__icon_mob {
	@include bp($point_4 - 1) {
		position: absolute;
		top: 64.2rem;
		right: 2rem;

		z-index: 5;
	}
}
