.landing_section {
}

.landing_section__wrap {
	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, stretch);
	}
}

.landing_section__col {
	position: relative;

	display: flex;
	justify-content: center;
	align-items: flex-end;

	min-height: calc(var(--full-screen) - var(--height-header));
	padding: 12.6rem var(--gl-indent);

	&::after {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;

		z-index: 2;

		width: 100%;
		height: 44%;
		background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%);
	}

	@include bp($point_2, min) {
		width: 50%;
		flex-shrink: 0;
	}
}

.landing_section__img_in {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.landing_section__img {
	@include cover_img;
}

.landing_section__body {
	position: relative;
	z-index: 5;
	max-width: 49.6rem;

	text-align: center;
}

.landing_section__logo {
	width: auto;

	.landing_section__col:nth-child(2n + 1) & {
		width: 18.8rem;
		margin-left: auto;
		margin-right: auto;
	}

	&:not(:last-child) {
		margin-bottom: 3.6rem;
	}

	@include bp($point_2, min) {
		height: 4.5rem;
	}

	@include bp($point_3) {
		height: 3.2rem;
	}
}

.header_logo__img {
	@include contain_img;
}

.landing_section__text {
	line-height: 1.44;
	font-style: italic;

	color: $white;

	&:not(:last-child) {
		margin-bottom: 3.6rem;
	}

	@include bp($point_2, min) {
		font-size: 1.8rem;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
	}
}
