.rich_text__wrap {
	@include flex_block(row, wrap, flex-start, center);

	&:not(:last-child) {
		margin-bottom: 3rem;
	}
}

.rich_text {
	&.rich_text--border_mod {
		border-top: 1px solid $black;

		@include bp($point_2, min) {
			padding-top: 1.2rem;
		}
		@include bp($point_3) {
			padding-top: 0.6rem;
		}
	}

	&.rich_text--width_mod {
		@include bp($point_2, min) {
			width: 81%;
		}

		@include bp($point_3) {
			width: 100%;
		}
	}

	&.rich_text--ofset_mod {
		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 5.6rem;
			}

			@include bp($point_3) {
				margin-bottom: 2.6rem;
			}
		}
	}

	&.rich_text--ofset_2_mod {
		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 5.6rem;
		}

		@include bp($point_3) {
			margin-bottom: 3rem;
		}
	}
}

h1.rich_text__title {
	@include h1;

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 2.2rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 0.8rem;
		}
	}
}

h2.rich_text__title {
	@include h2;

	.services & {
		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}

	&:not(:last-child) {
		margin-bottom: 3rem;
	}

	.rich_text__wrap & {
		margin-bottom: 0;
		&:not(:last-child) {
			margin-right: 2rem;
		}
	}
}

.rich_text__title_link {
	position: relative;
	margin-left: auto;

	@include text_2;

	color: $black;

	&:after {
		content: '';
		position: absolute;
		left: 0;
		bottom: -0.2rem;

		width: 100%;
		height: 1px;

		background-color: $black;
		opacity: 0;

		transition: opacity 0.3s;
	}

	&:hover,
	&:focus {
		text-decoration: none;

		&:after {
			opacity: 1;
		}
	}
}

.rich_text__text {
	@include text;

	p {
		.rich_text--style_mod & {
			&:not(:last-child) {
				margin-bottom: 1rem;
			}
		}

		.rich_text--style_2_mod & {
			&:not(:last-child) {
				@include bp($point_2, min) {
					margin-bottom: 3rem;
				}

				@include bp($point_3) {
					margin-bottom: 2.2rem;
				}
			}
		}
		.single_section & {
			&:not(:last-child) {
				margin-bottom: 2.8rem;
			}
		}
	}
}

.rich_text__btn {
	margin: 0 0 auto auto;

	@include bp($point_4 - 1) {
		width: 100%;
	}
}
