.footer {
	width: 100%;
	min-height: var(--height-footer);
	flex: 0 0 auto;

	font-size: 1rem;

	will-change: transform;

	@include bp($point_2, min) {
		padding: 14rem var(--gl-indent) 3rem;
	}

	@include bp($point_3) {
		padding: 6rem var(--gl-indent) 3rem;
	}
}

.footer__in {
	max-width: $page_width;
	width: 100%;
	margin: 0 auto;
}

.footer__wrap {
	border-top: 1px solid $black;

	.body--black_mod & {
		border-top: 1px solid $white;
	}

	@include bp($point_4, min) {
		@include flex_block(row, wrap, flex-start, flex-start);
		padding-top: 1.6rem;

		&:not(:last-child) {
			margin-bottom: 7.6rem;
		}
	}

	@include bp($point_4 - 1) {
		padding-top: 0.8rem;

		&:not(:last-child) {
			margin-bottom: 9.6rem;
		}
	}
}

.footer__logo {
	@include bp($point_4, min) {
		width: 12%;
		flex-shrink: 0;
	}

	@include bp($point_4) {
		&:not(:last-child) {
			margin-bottom: 2.2rem;
		}
	}
}

.footer__nav {
	@include bp($point_2, min) {
		&:not(:last-child) {
			padding-right: 3.4rem;
		}
	}

	@include bp($point_3, $point_4) {
		&:not(:last-child) {
			padding-right: 2rem;
		}
	}

	@include bp($point_4, min) {
		width: 47.4%;
		flex-shrink: 0;
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 4rem;
		}
	}
}

.footer__list {
	@include bp($point_2, min) {
		margin: 0 -5rem -5rem 0;
	}

	@include bp($point_3, $point_4) {
		margin: 0 -3rem -3rem 0;
	}

	@include bp($point_4, min) {
		@include flex_block(row, wrap, flex-start, flex-start);
	}
}

.footer__item {
	@include bp($point_2, min) {
		width: 25%;
		padding: 0 5rem 5rem 0;
	}

	@include bp($point_3, $point_4) {
		width: 50%;
		padding: 0 3rem 3rem 0;
	}

	@include bp($point_4, min) {
		flex-shrink: 0;
	}
}
.footer_menu {
	&.footer_menu--desctop_mod {
		@include bp($point_4 - 1) {
			display: none;
		}
	}

	&.footer_menu--mob_mod {
		@include bp($point_4, min) {
			display: none;
		}
	}

	@include bp($point_4 - 1) {
		padding: 1rem 0;
		border-bottom: 1px solid $black;

		.body--black_mod & {
			border-bottom: 1px solid $white;
		}
	}
}

.footer_menu__wrap {
	@include bp($point_4 - 1) {
		@include transition_all;
		width: 100%;
		max-height: 0;
		overflow: hidden;
	}
}

.footer_menu__list {
	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: 2.4rem;
		}
	}

	@include bp($point_4 - 1) {
		.footer_menu & {
			padding-top: 1rem;
		}
	}
}

.footer_menu__title {
	position: relative;

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: 2.4rem;
		}
	}
}

.footer_menu__title_arrow {
	@include bp($point_4, min) {
		display: none;
	}

	@include bp($point_4 - 1) {
		@include transition_transform;

		position: absolute;
		top: 0.8rem;
		right: 1.2px;
		width: 1.2rem;
		height: 1.2rem;

		pointer-events: none;

		.footer_select--open_state & {
			transform: rotate(-180deg);
		}
	}
}

.footer_menu__item {
	span {
		display: block;
	}

	&.footer_menu__item--mt_mod {
		@include bp($point_4, min) {
			margin-top: 2.4rem;
		}
	}

	@include bp($point_4 - 1) {
		padding: 1rem 0;
		&:not(.footer_menu__wrap &) {
			border-bottom: 1px solid $black;

			.body--black_mod & {
				border-bottom: 1px solid $white;
			}
		}

		.footer_menu & {
			padding: 0;
			&:not(:last-child) {
				margin-bottom: 0.6rem;
			}
		}
	}
}

.footer_menu__title,
.footer_menu__adress,
.footer_menu__link {
	font-size: 1.4rem;
	font-weight: 400;
	letter-spacing: 0.01em;
	line-height: 1.7;
	color: $black;

	.body--black_mod & {
		color: $white;
	}

	&.footer_menu__title--mob_mod {
		@include bp($point_4, min) {
			display: none;
		}
	}
}

.footer_menu__link {
	position: relative;

	&:after {
		content: '';
		position: absolute;
		left: 0;
		bottom: -0.2rem;

		width: 100%;
		height: 1px;

		background-color: $black;
		opacity: 0;

		transition: opacity 0.3s;

		.body--black_mod & {
			background-color: $white;
		}
	}

	&:hover,
	&:focus {
		text-decoration: none;

		&:after {
			opacity: 1;
		}
	}
}

.footer__row {
}

.footer__col {
}

.footer__social {
	@include bp($point_4, min) {
		width: 17.4%;
		flex-shrink: 0;
		margin-top: -0.2rem;

		&:not(:last-child) {
			padding-right: 2rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 4rem;
		}
	}
}

.footer__social_list {
	@include flex_block(row, wrap, flex-start, center);

	margin: 0 -1.6rem -1.6rem 0;
}

.footer__social_item {
	flex-shrink: 0;
	padding: 0 1.6rem 1.6rem 0;
}

.footer__social_link {
	display: block;
	color: $black;

	.body--black_mod & {
		color: $white;
	}

	transition: color 0.3s ease;

	.footer__social_item:nth-child(4n + 1) & {
		width: 1rem;
		height: 1.8rem;
	}

	.footer__social_item:nth-child(4n + 2) & {
		width: 1.6rem;
		height: 1.6rem;
	}

	.footer__social_item:nth-child(4n + 3) & {
		width: 1.6rem;
		height: 1.8rem;
	}

	.footer__social_item:nth-child(4n + 4) & {
		width: 1.5rem;
		height: 1.2rem;
	}

	// &:hover,
	// &:focus {
	// 	color: $gray_3;
	// }
}

.footer__info {
	@include bp($point_4, min) {
		width: 23.2%;
		flex-shrink: 0;

		padding-top: 0.4rem;
	}

	@include bp($point_4 - 1) {
	}
}

.footer__info__title,
.footer__info__text {
	font-size: 1.4rem;
	font-weight: 400;
	letter-spacing: 0.01em;
	line-height: 1.14;

	.body--black_mod & {
		color: $white;
	}
}

.footer__info__title {
	text-transform: uppercase;

	&:not(:last-child) {
		margin-bottom: 1.8rem;
	}
}

.footer__info__text {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 2.2rem;
		}
	}

	@include bp($point_3 - 1) {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.footer__form_input {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 2.4rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 4rem;
		}
	}
}

.footer__form_in {
	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, center);
	}
}

.footer__form_checkbox {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-right: 1rem;
		}
	}
	@include bp($point_3, $point_4) {
		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}
}

.footer__bottom {
}

.footer__copyright {
	font-size: 1.4rem;
	font-weight: 400;
	letter-spacing: 0.01em;
	line-height: 1.14;

	.body--black_mod & {
		color: $white;
	}
}
