@import 'ScssHelpers/helpers-all.scss';

.product__info {
	@include flex_block(row, wrap, flex-start, flex-start, flex-start);

	@include bp($point_2, min) {
		margin-bottom: 10.2rem;
	}
}

.product__col {
	width: 100%;

	@include flex_block(row, wrap, flex-start, flex-start, flex-start);

	&:nth-child(2n + 1) {
		@include bp($point_3) {
			margin-bottom: 5.7rem;
		}
	}

	&:nth-child(2n + 2) {
		flex-direction: row-reverse;

		@include bp($point_3) {
			margin-bottom: 5.6rem;
		}
	}
}

.product__picture_wrap {
	.product__col:nth-child(2n + 1) & {
		@include bp($point_2, min) {
			width: 51.2%;
			padding-right: 22rem;
		}

		@include bp($point_3) {
			width: 100%;
		}
	}

	.product__col:nth-child(2n + 2) & {
		@include bp($point_2, min) {
			width: 48.8%;
			margin-top: -18.3rem;
		}

		@include bp($point_3) {
			width: 100%;
		}
	}
}

.product__picture {
	position: relative;
	display: block;

	width: 100%;

	overflow: hidden;

	.product__col:nth-child(2n + 2) & {
		padding-bottom: calc(501 / 800 * 100%);
	}

	.product__col:nth-child(2n + 1) & {
		padding-bottom: calc(821 / 656 * 100%);
	}

	&:not(:last-child) {
		margin-bottom: 0.8rem;
	}
}

.product__img_in {
	display: block;
	width: 100%;
	height: 100%;

	&:not(:last-child) {
		margin-bottom: 1rem;
	}
}

.product__img {
	@include cover_img;
	@include hover_img('.product__picture');

	position: absolute;
	top: 0;
	left: 0;
}

.product__img_name {
	display: block;

	@include text_2;

	color: $black;
}

.product__text {
	@include text;

	.product__col:nth-child(2n + 1) & {
		@include bp($point_2, min) {
			width: 48.8%;
			padding-top: 20.5rem;
			padding-bottom: 27rem;
		}
	}

	.product__col:nth-child(2n + 2) & {
		@include bp($point_2, min) {
			width: 51.2%;
			padding-top: 18.3rem;
			padding-right: 16rem;

			&.product__text--mt_mod {
				padding-top: 0;
			}
		}
	}

	p {
		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 5.4rem;
			}

			@include bp($point_3) {
				margin-bottom: 4.4rem;
			}
		}
	}

	@include bp($point_3) {
		&:not(:first-child) {
			padding-top: 6.1rem;
		}
	}
}
