@import "ScssHelpers/helpers-all";

.form_select .ts-wrapper.single .ts-control, .ts-wrapper.single .ts-control input {
	display: block;
	width: 100%;

	font-weight: 300;
	
	background-color: transparent;
	border: none;
	border-radius: 0;
	background-clip: padding-box;

	box-shadow: inset 0 -1px 0 0 $black;

	transition: box-shadow 0.3s ease;

	@include bp($point_2, min) {
		padding: 0;
		height: 5.5rem;
	}

	@include bp($point_3) {
		height: 3.7rem;
		padding: 0;
	}

	&::-webkit-input-placeholder {
		color: $black;
	}
	&::-moz-placeholder,
	&:-moz-placeholder {
		color: $black;
		opacity: 1;
	}
	&:-ms-input-placeholder {
		color: $black;
	}

	.form_sec_in--color_mod & {
		box-shadow: inset 0 -1px 0 0 $white;
		color: $white;
	}
}

.form_select .ts-control, .form_select .ts-wrapper.single.input-active .ts-control {
	cursor: pointer;
}

.contact_form .form_select .ts-control {
	display: flex !important; //override plugin styles
	align-items: center;
}

.form_select .ts-wrapper.single .ts-control, .ts-wrapper.single .ts-control input,
.form_select .ts-dropdown .option {
	font-size: 1.6rem;
	font-family: $font_1;
	color: $black;
}

.ts-dropdown div[data-value="Placeholder"] {
	display: none;
}

.form_select .ts-wrapper {
	position: relative;
	background: unset;
	background-repeat: no-repeat;
	background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACAAAAAZBAMAAACiITGiAAAAAXNSR0IB2cksfwAAACFQTFRFAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAbdFIwQAAAAt0Uk5TAEzvbCvOjQqurfTFWbgxAAAAN0lEQVR4nGNgGNRAAEQwIviMyiDSSAAh4prAwMAWgqSFvbMAiJANYQsBKUIGVovR7GEUYBjkAABpZAVkNByEwAAAAABJRU5ErkJggg==');
	background-position: right center;

	&.dropdown-active {
		background-image: url('data:image/png;base64,');
	}

	.form_sec_in--color_mod & {
		background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACAAAAAZBAMAAACiITGiAAAAAXNSR0IB2cksfwAAACFQTFRFAAAA////////////////////////////////////////zyHr+QAAAAt0Uk5TAEzvbCvOjQqurfTFWbgxAAAAN0lEQVR4nGNgGNRAAEQwIviMyiDSSAAh4prAwMAWgqSFvbMAiJANYQsBKUIGVovR7GEUYBjkAABpZAVkNByEwAAAAABJRU5ErkJggg==');
	}
	.form_sec_in--color_mod &.dropdown-active {
		background-image: url('data:image/png;base64,');
	}
}

.form_select .ts-control, .form_select .ts-wrapper.single.input-active .ts-control {
	background: transparent;
}

.form_select .ts-dropdown .option {
	position: relative;
	padding: .8rem 1rem .8rem 3rem;
	font-weight: 400;
	
	transition: background-color .15s ease, color .15s ease;

	&:hover {
		background-color: $black;
		color: $white;
	}

	&:not(:last-child)::after {
		content: "";
		position: absolute;
		bottom: 0;
		left: 50%;

		display: block;
		height: 1px;
		width: 100%;

		background-color: $black;
		transform: translateX(-50%);
	}
}

.form_select .ts-dropdown .option.selected {
	&::before {
		content: "";
		position: absolute;
		left: 1rem;
		top: 47%;

		display: block;
		width: 0.8rem;
		height: 1.4rem;

		border: 2px solid $black;
		border-top: 0;
		border-left: 0;
		transform: rotate(45deg) translate(-58%, -58%);
		transition: border-color .15s ease;
	}

	&:hover::before {
		border-color: $white;
	}
}

.form_select .ts-dropdown {
	margin-top: -.1rem;
	background: unset;
	background-color: $gray_3;
	box-shadow: none;
	border: 1px solid $black;
	border-top: none;
	border-radius: 0;

	.body--black_mod & {
		background: $white;
	}
}

.form_select .ts-dropdown .active {
	background-color: transparent;
}

.form_select .ts-control .item {
	.body--black_mod & {
		color: $white;
	}
}