@import 'ScssHelpers/helpers-all.scss';

.store__block_head {
	@include flex_block(row, wrap, flex-start, flex-start, flex-start);

	@include bp($point_2, min) {
		margin-bottom: 5rem;
	}

	@include bp($point_3) {
		margin-bottom: 3rem;
	}
}

.store__block_head_btn_wrap {
	@include bp($point_3) {
		@include flex_block(row, wrap, flex-start, flex-start, flex-start);
		width: 100%;
	}
}

.store__block_head_btn {
	@include btn_size_normal;
	@include btn_style_white_border;

	@include bp($point_2, min) {
		padding: 0 5rem !important;
	}

	@include bp($point_3) {
		padding: 0 2rem !important;
	}

	&.store__block_head_btn--indent_mod {
		@include bp($point_2, min) {
			display: none;
		}

		@include bp($point_3) {
			margin-left: auto;
			padding: 0 5rem !important;
		}
	}

	.store--contacts_mod & {
		@include bp($point_3) {
			margin: 0 auto;
			width: 100%;
		}
	}
}

.store__block_head_btn_icon {
	width: 1.8rem;
	height: 1.8rem;

	margin-right: 1rem;
}

.store__block_head_filters {
	@include flex_block(row, wrap, flex-start, stretch, flex-start);

	.store--contacts_mod & {
		@include bp($point_2, min) {
			margin-left: 0;
		}
	}
	@include bp($point_2, min) {
		margin-left: auto;
	}

	@include bp($point_3) {
		display: none;
		flex-direction: column;
		width: 100%;
	}
}

.store__block_head_filters_in {
	min-width: 24rem;

	&:not(:last-child) {
		margin-right: 4rem;
	}
}

.store__block_list {
	@include bp($point_2, min) {
		margin-bottom: 2.6rem;
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.store__block_list_item {
}

.store__block_btn_wrap {
	text-align: center;
}

.store__block_btn {
	@include btn_size_small;
	@include btn_style_white_border;

	@include bp($point_2, min) {
		padding: 0 3rem !important;
	}

	@include bp($point_3) {
		padding: 0 3.4rem !important;
		font-size: 1.8rem;
	}
}
