.breadcrumbs {
	background-color: $black;

	.body--black_mod & {
		background: $white;
	}

	@include bp($point_4, min) {
		padding: 0.6rem var(--gl-indent);
	}

	@include bp($point_4 - 1) {
		padding: 0.8rem var(--gl-indent);
	}
}

.breadcrumbs__in {
	max-width: $page_width;
	width: 100%;
	margin: 0 auto;
}

.breadcrumbs_list {
	@include flex_block(row, wrap, flex-start, center);

	@include bp($point_4, min) {
		margin: 0 -1.2rem -1.2rem 0;
	}

	@include bp($point_4 - 1) {
		margin: 0 -1rem -1rem 0;
	}
}

.breadcrumbs_item {
	@include flex_block(row, nowrap, flex-start, center);

	@include bp($point_4, min) {
		padding: 0 1.2rem 1.2rem 0;
	}

	@include bp($point_4 - 1) {
		padding: 0 1rem 1rem 0;
	}
}

.breadcrumbs_link {
	position: relative;

	letter-spacing: 0.01em;
	line-height: 1.2;
	color: $white;

	.body--black_mod & {
		color: $black;
	}

	transition: color 0.3s ease;

	&:after {
		content: '';
		position: absolute;
		left: 0;
		bottom: 0;

		width: 100%;
		height: 1px;

		background-color: $white;
		opacity: 0;

		transition: opacity 0.3s;

		.body--black_mod & {
			background-color: $black;
		}
	}

	&:hover,
	&:focus {
		text-decoration: none;

		&:after {
			opacity: 1;
		}
	}

	&.breadcrumbs_link--current_mod {
		pointer-events: none;
	}

	@include bp($point_4, min) {
		font-size: 1.4rem;
		font-weight: 400;

		&:not(:last-child) {
			margin-right: 1.2rem;
		}
	}

	@include bp($point_4 - 1) {
		font-size: 1.2rem;
		font-weight: 300;

		&:not(:last-child) {
			margin-right: 0.9rem;
		}
	}
}

.breadcrumbs_icon {
	flex-shrink: 0;
	width: 0.8rem;
	height: 0.8rem;
	color: $white;

	.body--black_mod & {
		color: $black;
	}
}
