@import 'ScssHelpers/helpers-all.scss';

.about {
	@include bp($point_2, min) {
		padding-top: 10.4rem;
	}

	@include bp($point_3) {
		padding-top: 6rem;
	}

	.btn_v1 {
		@include bp($point_4 - 1) {
			width: 100%;
			border-color: $black;
		}
	}
}

.about__title {
	@include h1;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2rem;
		}

		@include bp($point_3) {
			margin-bottom: 3rem;
		}
	}
}

.about__text {
	@include text;

	&:not(:last-child) {
		margin-bottom: 2.8rem;
	}
}

.about__btn {
	@include btn_size_normal;
	@include btn_style_white;
}
