@import 'ScssHelpers/helpers-all.scss';

.contact_form {
	width: 100%;

	font-family: $font_1;
	color: $black;

	&.contact_form--width_mod {
		max-width: 93rem;
		margin: 0 auto;
	}
}

.contact_form__title {
	@include h2;
	position: relative;

	@include bp($point_2, min) {
		text-align: center;
		margin-bottom: 3.5rem;
	}

	@include bp($point_3) {
		text-align: left;
		margin-bottom: 1.8rem;
	}

	&.contact_form__title--align_mod_1 {
		@include bp($point_2, min) {
			text-align: left;
		}
	}

	.contact_form--style_mod_1 & {
		@include bp($point_2, min) {
			margin-bottom: 3.2rem;
		}
	}

