	// 	&::after {
	// 		content: "";
	// 		position: absolute;
	// 		bottom: -3.2rem;
	// 		left: 0;

	// 		display: block;
	// 		width: 100%;
	// 		height: 1px;

	// 		background-color: $black;

	// 		@include bp($point_3) {
	// 			display: none;
	// 		}
	// 	}

	// 	&:not(:last-child) {

	// 		@include bp($point_2, min) {
	// 			margin-bottom: 4.2rem;
	// 		}

	// 		@include bp($point_3) {
	// 			margin-bottom: 3.6rem;
	// 		}
	// 	}
	// }

	.body--black_mod & {
		color: $white;

		&::after {
			background-color: $white;
		}
	}
}

.contact_form__inputs_list {
	@include flex_block(column, nowrap, flex-start, stretch);

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 3.8rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 2.8rem;
		}
	}
}

.contact_form__inputs_item {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: .2rem;
		}

		@include bp($point_3) {
			margin-bottom: 1rem;
		}
	}
}

.contact_form__checkboxes {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.2rem;
		}
	}
}

.contact_form__checkboxes_item {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 0;
		}

		@include bp($point_3) {
			margin-bottom: 1.8rem;
		}
	}
}

.contact_form__descr {
	font-weight: 300;

	@include bp($point_2, min) {
		font-size: 1.6rem;
		line-height: 2rem;
		&:not(:last-child) {
			margin-bottom: 3.6rem;
		}
	}

	@include bp($point_3) {
		font-size: 1.2rem;
		line-height: 1.5rem;
		letter-spacing: 0.01em;
		
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}

	.body--black_mod & {
		color: $white;
	}
}

.contact_form__btn_block {
	@include flex_block(row, nowrap, center, stretch);

	.contact_form--style_mod_1 & {
		@include flex_block(row, nowrap, flex-start, stretch);

		@include bp($point_2, min) {
			margin-top: 5rem;
		}

		@include bp($point_3) {
			margin-top: 3rem;
		}
	}
}

.contact_form__btn {
	&.contact_form__btn--style_mod {
		min-width: 9.5rem;
	}
}
