.hero {
	position: relative;

	min-height: calc(var(--full-screen) - var(--height-header));

	@include flex_block(row, nowrap, flex-end, flex-end);

	@include bp($point_2, min) {
		padding: 3rem 14rem;
	}

	@include bp($point_3, $point_4) {
		padding: 3rem 4rem;
	}
}

.hero__bg_in {
	@include stretch;
}

.hero__img {
	@include cover_img;
}

.hero__link {
	@include bp($point_4 - 1) {
		display: none;
	}
}

.hero__link_icon {
	position: absolute;
	top: 50%;
	left: 2.6rem;

	width: 2.2rem;
	height: 2rem;

	transform: translateY(-50%);
}

.hero__link_icon_mob {
	@include bp($point_4 - 1) {
		position: absolute;
		bottom: 2.8rem;
		right: 2rem;
	}
}
