@import 'ScssHelpers/helpers-all.scss';

.news__body {
	.news--slider_mod & {
		@include bp($point_4, min) {
			margin-right: -4rem;
		}
	}

	& .news__list.news--grid_mod {
		@include bp($point_2, min) {
			margin-bottom: 2.2rem;
		}

		@include bp($point_3, $point_4) {
			margin-bottom: 1rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 3rem;
		}
	}
}

.news__list {
	&.news--grid_mod {
		@include bp($point_2, min) {
			margin: 0 -4rem -7.2rem 0;
		}

		@include bp($point_3, $point_4) {
			margin: 0 -4rem -4rem 0;
		}
	}

	@include bp($point_4, min) {
		@include flex_block(row, wrap, flex-start, stretch, stretch);

		margin: 0 -4rem -4rem 0;
	}
}

.news__item {
	.news--img_mod & {
		display: flex;
		flex-direction: column;
	}

	.news--grid_mod & {
		@include bp($point_2, min) {
			width: 33.33%;
			flex-shrink: 0;
			padding: 0 4rem 7.2rem 0;
		}

		@include bp($point_3, $point_4) {
			width: 50%;
			flex-shrink: 0;
			padding: 0 4rem 4rem 0;
		}

		@include bp($point_4 - 1) {
			&:not(:last-child) {
				margin-bottom: 4.5rem;
			}
		}
	}

	.mobile_slider & {
		@include bp($point_4) {
			width: 25.6rem;
			padding: 0;

			&:not(:last-child) {
				margin-right: 2rem;
			}
		}
	}

	// .news--slider_mod & {
	// 	@include bp($point_4 - 1) {
	// 		&:not(:last-child) {
	// 			margin-right: 1.8rem;
	// 		}
	// 	}
	// }

	@include bp($point_4, min) {
		width: 50%;
		flex-shrink: 0;

		padding: 0 4rem 4rem 0;
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 4rem;
		}
	}
}

.news__item_data {
	@include text_2;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 0.4rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 0.6rem;
		}
	}
}

.news__item_link {
	display: block;
	overflow: hidden;

	margin-top: auto;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 3.4rem;
		}

		.news--style_mod,
		.news--grid_mod & {
			&:not(:last-child) {
				margin-bottom: 2.6rem;
			}
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.news__item_img_in {
	position: relative;
	display: block;
	width: 100%;
	padding-bottom: 66.4%;

	.news--img_mod & {
		padding-bottom: 178%;
	}
}

.news__item_img {
	position: absolute;
	top: 0;
	left: 0;

	@include cover_img;
	@include hover_img('.news__item_link');
}

.news__item_wrap {
	@include flex_block(row, nowrap, flex-start, center);
}

.news__item_title {
	@include bp($point_2, min) {
		.news--style_mod & {
			&:not(:last-child) {
				margin-bottom: 1.6rem;
			}
		}
	}
	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 0.4rem;
		}
	}
}

.news__item_title_link {
	position: relative;
	// display: block;

	color: $black;

	@include h3;

	transition: color 0.3s ease;

	&:after {
		content: '';
		position: absolute;
		left: 0;
		bottom: -0.2rem;

		width: 100%;
		height: 1px;

		background-color: $black;
		opacity: 0;

		transition: opacity 0.3s;
	}

	.news--style_mod & {
		font-size: 2.8rem;
	}

	&:hover,
	&:focus {
		text-decoration: none;

		&:after {
			opacity: 1;
		}
	}
}

.news__item_descr {
	position: relative;
	margin-left: auto;

	@include text_2;

	color: $black;

	transition: color 0.3s ease;

	&:after {
		content: '';
		position: absolute;
		left: 0;
		bottom: -0.2rem;

		width: 100%;
		height: 1px;

		background-color: $black;
		opacity: 0;

		transition: opacity 0.3s;
	}

	.news--style_mod & {
		font-size: 2.8rem;
	}

	&:hover,
	&:focus {
		text-decoration: none;

		&:after {
			opacity: 1;
		}
	}
}

.news__item_text {
	&:not(.news--style_mod &) {
		max-width: 62.2rem;
	}

	@include text;

	&:not(:last-child) {
		margin-bottom: 2.8rem;
	}
}

.news__btn_wrap {
	text-align: center;
}
