//---------------------------------------------fonts
$font_path: '../fonts/';
$font_1: 'ReplicaPro', sans-serif;
$modern: 'Modern', sans-serif;
$font_size_base: 1.6rem;
//---------------------------------------------fonts###

//---------------------------------------------colors
$white: #fff;
$black: #000;

$beige: #faf9f5;

$gray: #ebecec;
$gray_2: #707070;
$gray_3: #E0DDD2;

$color_text_base: $black;
$color_link_base: #4aa3df;
$body_bg: $beige;
//---------------------------------------------colors###

//---------------------------------------------z-index
$z-index: (
	popup: 101,
	header: 100,
	footer: 100,
);
//---------------------------------------------z-index###

//---------------------------------------------layout
$page_width: 164rem;

//---------------------------------------------layout###

//---------------------------------------------global values
$gl_radius: 0.5rem;
//---------------------------------------------global values###

//---------------------------------------------media points
$point_1: 1200px;
$point_2: 1024px;
$point_3: 1023px;
$point_4: 768px;
$point_5: 640px;
$point_6: 480px;
$point_7: 375px;
$point_8: 320px;
$point_9: 1366px;
$point_10: 1440px;
$point_11: 1600px;
//---------------------------------------------media points

//---------------------------------------------forms
$input_height: 3.2rem;
$input_offset: 0;
$input_font_size: 1.6rem;
$input_border_color: $black;
$input_border_color_active: $black;
$input_placeholder_color: rgba($black, 0.4);
$input_text_color: $black;
$input_radius: 0;
