.hero_v5 {
}

.hero_v5__wrap {
	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, center);
	}

	.hero_v5--flex_mod & {
		@include bp($point_2, min) {
			@include flex_block(row, nowrap, flex-start, flex-start);
		}
	}

	.hero_v5--flex_1_mod & {
		@include bp($point_2, min) {
			@include flex_block(row, nowrap, flex-start, flex-start);
		}
	}
}

.hero_v5__pic {
	position: relative;

	@include bp($point_2, min) {
		width: 74.4%;
	}

	// @include bp($point_3, $point_4) {
	// 	width: 60%;
	// 	align-self: start;
	// }

	@include bp($point_2, min) {
		flex-shrink: 0;
		padding-bottom: 42.8%;
	}

	@include bp($point_3) {
		padding-bottom: 57.6%;

		&:not(:last-child) {
			margin-bottom: 2.8rem;
		}
	}
}

.hero_v5__img_in {
	@include stretch;
}

.hero_v5__img {
	@include cover_img;
}

.hero_v5__img_list {
	@include bp($point_2, min) {
		width: 65.8%;
	}

	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 6rem;
		}
	}
}

.hero_v5__img_item {
	position: relative;

	@include bp($point_9, min) {
		padding-top: 86.2%;
	}

	@include bp($point_9 - 1, $point_2) {
		padding-top: 100%;
	}

	@include bp($point_3) {
		padding-top: 140%;
	}

	@include bp($point_2, min) {
		max-width: 66rem;
		margin-left: auto;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 5rem;
		}

		@include bp($point_3) {
			margin-bottom: 2.4rem;
		}
	}
}

.hero_v5__info {
	@include bp($point_2, min) {
		width: 26rem;
		flex-shrink: 0;
		padding-left: 2rem;
		margin-left: auto;
	}

	.hero_v5--flex_mod & {
		@include bp($point_2, min) {
			margin-top: 25.8rem;
		}
	}
}

.hero_v5__info_wrap {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 7rem;
		}

		@include bp($point_3) {
			margin-bottom: 2.8rem;
		}
	}
}

.hero_v5__list {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 7.2rem;
		}
	}
	@include bp($point_3, $point_4) {
		&:not(:last-child) {
			margin-bottom: 5rem;
		}
	}
	@include bp($point_3) {
		@include flex_block(row, wrap, flex-start, flex-start);

		margin: 0 -2.2rem -2.2rem 0;
	}
}

.hero_v5__item {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 2.8rem;
		}
	}
	@include bp($point_3) {
		width: 50%;
		flex-shrink: 0;
		padding: 0 2.2rem 2.2rem 0;
	}
}

.hero_v5__item_heading,
.hero_v5__item_descr {
	@include text;
}

.hero_v5__item_link_w {
	&.hero_v5__item_link_w--mt_mod {
		@include bp($point_4, min) {
			margin-top: 2.6rem;
		}

		@include bp($point_4 - 1) {
			margin-top: 2.2rem;
		}
	}
}

.hero_v5__item_link {
	position: relative;

	@include text;

	color: $black;

	&:not(.hero_v5__item_descr--text_mod) {
		&:after {
			content: '';
			position: absolute;
			left: 0;
			bottom: -0.2rem;

			width: 100%;
			height: 1px;

			background-color: $black;
			opacity: 0;

			transition: opacity 0.3s;
		}

		&:hover,
		&:focus {
			text-decoration: none;

			&:after {
				opacity: 1;
			}
		}
	}

	&.hero_v5__item_link--text_mod {
		&:after {
			content: '';
			position: absolute;
			left: 0;
			bottom: -0.2rem;

			width: 100%;
			height: 1px;

			background-color: $black;
			opacity: 1;

			transition: opacity 0.3s;
		}

		&:hover,
		&:focus {
			text-decoration: none;

			&:after {
				opacity: 0;
			}
		}
	}
}

.hero_v5__item_heading {
	display: block;
	font-weight: 400;
}

.hero_v5__store {
	@include flex_block(row, wrap, flex-start, center);

	&:not(:last-child) {
		margin-bottom: 7rem;
	}

	&.hero_v5__store--desctop_mod {
		@include bp($point_4 - 1) {
			display: none;
		}
	}

	&.hero_v5__store--mobile_mod {
		@include bp($point_4, min) {
			display: none;
		}
	}
}

.hero_v5__store_list {
	flex-shrink: 0;

	@include flex_block(row, wrap, flex-start, center);

	&:not(:last-child) {
		padding-right: 1rem;
	}

	@include bp($point_4, min) {
		margin: 0 -1.8rem -1.8rem 0;
	}

	@include bp($point_4 - 1) {
		margin: 0 -2rem -2rem 0;
	}
}

.hero_v5__store_item {
	flex-shrink: 0;

	@include bp($point_4, min) {
		padding: 0 1.8rem 1.8rem 0;
	}

	@include bp($point_4 - 1) {
		padding: 0 2rem 2rem 0;
	}
}

.hero_v5__store_item_title {
	@include text;

	font-weight: 400;

	&:not(:last-child) {
		margin-bottom: 2rem;
	}
}

.hero_v5__store_btn {
	display: block;
	width: 4rem;
	height: 4rem;

	padding: 0.8rem 0.8rem;
	color: $black;

	border-radius: 50%;
	border: 1px solid $black;
	background-color: $white;

	cursor: pointer;

	transition: background 0.3s ease;

	.hero_v5__store_item:nth-child(2n + 2) & {
		padding: 1rem 1rem;
	}

	&:hover,
	&:focus {
		background-color: $gray_3;
	}
}

.hero_v5__store_text {
	position: relative;
	text-align: right;
	color: $black;

	cursor: pointer;

	@include text_3;
	@include transition_color;

	margin-left: auto;

	&:after {
		content: '';
		position: absolute;
		left: 0;
		bottom: 0;

		width: 100%;
		height: 1px;

		background-color: $black;
		opacity: 0;

		transition: opacity 0.3s;
	}

	&:hover,
	&:focus {
		text-decoration: none;

		&:after {
			opacity: 1;
		}
	}
}

.hero_v5__btn {
	width: 100%;
	@include flex_block(row, nowrap, center, center);

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.hero_v5__link {
	@include text_2;

	font-weight: 500;
	color: $black;
	font-family: $modern;
}

.hero_v5__link_icon {
	width: 2.2rem;
	height: 2rem;

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-right: 2.4rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-right: 1rem;
		}
	}
}
