@mixin cover_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: cover;
}

@mixin contain_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: contain;
}

@mixin hover_img($class) {
	transform-origin: 50% 50%;
	transition: transform 0.6s;
	will-change: transform;

	#{$class}:hover &,
	#{$class}:focus & {
		transform: scale(1.1);
	}
}

@mixin hover_link() {
	position: relative;

	&:after {
		@include bp($point_2, min) {
			content: '';
			position: absolute;
			left: 0;
			bottom: -0.3rem;

			width: 100%;
			height: 1px;

			background-color: $black;
			opacity: 0;

			transition: opacity 0.3s;

			.body--black_mod & {
				background-color: $white;
			}
		}
	}

	&:hover,
	&:focus {
		text-decoration: none;

		&:after {
			opacity: 1;
		}
	}
}

@mixin popup {
	@include flex_block(row, nowrap, flex-start);

	position: fixed;
	top: 0;
	left: 0;

	width: 100%;
	height: var(--full-screen);

	overflow-y: auto;

	background-color: rgba($black, 0.75);

	opacity: 0;
	pointer-events: none;

	z-index: z-index(popup);

	&.popup--ready_state {
		transition: opacity 0.3s ease; // значение милисекунд должно совпадать с "const timeout" (300) в initPopup.js
	}

	&.popup--active_state {
		opacity: 1;
		pointer-events: initial;
	}

	@include bp($point_2, min) {
		padding: 7.1rem var(--gl-indent);
	}

	@include bp($point_3) {
		padding-top: 3.5rem;
	}

	&.popup--mobile_mod {
		@include bp($point_2, min) {
			display: none;
		}
	}
}
