@import 'ScssHelpers/helpers-all.scss';

.press_area_hero {
	padding-bottom: 0;
}

.press_area_hero__accordion {
	border-top: 1px solid $black;
}

.press_area_hero__accordion_item_head {
	@include flex_block(row, wrap, flex-start, center, flex-start);

	color: $black;

	background-repeat: no-repeat;
	background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACAAAAAZBAMAAACiITGiAAAAAXNSR0IB2cksfwAAACFQTFRFAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAbdFIwQAAAAt0Uk5TAEzvbCvOjQqurfTFWbgxAAAAN0lEQVR4nGNgGNRAAEQwIviMyiDSSAAh4prAwMAWgqSFvbMAiJANYQsBKUIGVovR7GEUYBjkAABpZAVkNByEwAAAAABJRU5ErkJggg==');
	background-position: right center;
	cursor: pointer;
	transition: border-color 0.3s;

	@include transition_color;

	@include bp($point_2, min) {
		padding: 2.3rem 0;
	}

	@include bp($point_3) {
		padding: 1rem 0 1rem;
	}

	&:hover,
	&:focus {
		color: lighten($black, 30%);
	}

	.press_area_hero__accordion--active_mod & {
		background-image: url('data:image/png;base64,');
	}
}

.press_area_hero__accordion_item_body {
	max-height: 0;
	opacity: 0;

	transition: all 0.25s ease-out;
	will-change: max-height;
	pointer-events: none;

	@include bp($point_2, min) {
		overflow: hidden; // for design purpose
	}

	.press_area_hero__accordion--active_mod & {
		opacity: 1;
		pointer-events: auto;
	}
}

.press_area_hero__accordion_item_content {
	@include bp($point_2, min) {
		padding: 3rem 0 11.2rem;
	}

	@include bp($point_3) {
		padding: 1.8rem 0 2.6rem;
	}
}

.press_area_hero__accordion_item_title,
.press_area_hero__accordion_item_title_mob {
	padding-right: 2rem;
	@include h2;
}

.press_area_hero__accordion_item_title {
	@include bp($point_3) {
		display: none;
	}
}

.press_area_hero__accordion_item_title_mob {
	@include bp($point_2, min) {
		display: none;
	}
}

.press_area_hero__accordion_item_icon {
	display: block;
	width: 1.8rem;
	height: 0.8rem;

	margin: 0 0 0 auto;

	transform-origin: center;
	transition: 0.3s;
	transform: rotateZ(0deg);

	.press_area_hero__accordion--active_mod & {
		transform: rotateZ(180deg);
	}

	.press_area_hero__accordion_item_head:hover & {
		color: lighten($black, 30%);
	}
}

.press_area_hero__accordion_item {
	&:not(:last-child) {
		border-bottom: 1px solid $black;
	}
}

//------------------------------ contacts list

.press_area_hero__contacts_list {
	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, flex-start, flex-start);
		margin: 0 -10rem -10rem 0;
	}
}

.press_area_hero__contacts_list_item {
	@include bp($point_2, min) {
		width: 33.33%;
		padding: 0 10rem 10rem 0;
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 2.6rem;
		}
	}
}

.press_area_hero__contacts_list_item_in {
	width: 100%;
}

.press_area_hero__contacts_company,
.press_area_hero__contacts_name,
.press_area_hero__contacts_email,
.press_area_hero__contacts_phone {
	font-weight: 300;

	@include bp($point_2, min) {
		margin-bottom: -0.2rem;

		//font-family: $modern;
		font-size: 2.2rem;
	}

	@include bp($point_3) {
		margin-bottom: 0.1rem;
		font-size: 1.6rem;
	}
}

.press_area_hero__contacts_company {
	@include bp($point_2, min) {
		//font-style: italic;
		font-weight: 500;
	}
	@include bp($point_3) {
		font-weight: 400;
	}
}

.press_area_hero__link {
	position: relative;
	display: block;
	width: max-content;
	color: $black;

	&:after {
		content: '';
		position: absolute;
		left: 0;
		bottom: 0.3rem;

		width: 100%;
		height: 1px;

		background-color: $black;
		opacity: 0;

		transition: opacity 0.3s;
	}

	&:hover,
	&:focus {
		text-decoration: none;

		&:after {
			opacity: 1;
		}
	}
}
