@mixin st_btn {
	display: inline-flex;
	justify-content: center;
	align-items: center;

	text-align: center;
	white-space: nowrap;
	font-family: $modern;

	cursor: pointer;

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

//color mixins:
@mixin btn_style_white {
	font-weight: 500;
	color: $black;
	letter-spacing: 0.16px;

	background-color: $white;
	transition: background-color 0.3s ease;
	border: 1px solid $white;

	@include st_btn;

	&:hover {
		background-color: $gray_3;
	}
}

@mixin btn_style_white_border {
	font-weight: 500;
	color: $black;
	letter-spacing: 0.16px;

	background-color: $white;
	border: 1px solid $black;
	transition: background-color 0.3s ease;

	@include st_btn;

	&:hover {
		background-color: $gray_3;
		// .body--black_mod & {
		// 	background-color: darken($gray, 50%);
		// }
	}

	.footer & {
		@include bp($point_4 - 1) {
			display: none;
		}
	}
}

//size mixins:
@mixin btn_size_normal {
	padding: 0 2.6rem;
	border-radius: 4rem;

	@include bp($point_2, min) {
		height: 5rem;

		line-height: 4.8rem;
		font-size: 1.6rem;
	}

	@include bp($point_3) {
		height: 4rem;

		line-height: 3.8rem;
		font-size: 1.4rem;
	}
}

@mixin btn_size_normal_2 {
	padding: 0 1.2rem;

	@include bp($point_2, min) {
		height: 5rem;
		min-width: 20.6rem;

		line-height: 4.8rem;
		font-size: 1.6rem;

		border-radius: 5rem;
	}

	@include bp($point_3) {
		height: 4rem;
		width: 100%;

		line-height: 3.8rem;
		font-size: 1.4rem;

		border-radius: 4rem;
	}
}

@mixin btn_size_small {
	height: 4rem;
	padding: 0 2.4rem;

	line-height: 3.8rem;
	font-size: 1.4rem;

	border-radius: 4rem;
}

@mixin btn_size_small_2 {
	height: 4rem;
	padding: 0 2.4rem;

	line-height: 3.8rem;
	font-size: 1.6rem;

	border-radius: 4rem;
}

@mixin btn_icon {
	@include bp($point_2, min) {
		width: 2.2rem;
		height: 2.2rem;
		margin-right: 2rem;
	}

	@include bp($point_3) {
		width: 1.6rem;
		height: 1.6rem;
		margin-right: 0.6rem;
	}
}

@mixin btn_mail {
	@include bp($point_4, min) {
		display: none;
	}

	@include bp($point_4 - 1) {
		display: flex;
		align-items: center;
		justify-content: center;

		width: 4rem;
		height: 4rem;

		padding: 1.2rem 1rem;

		background-color: $white;
		border-radius: 50%;
		border: 1px solid $black;
	}
}

@mixin fixed_btn {
	position: fixed;
	z-index: 10;

	display: flex;
	align-items: center;
	justify-content: center;

	@include bp($point_9 + 1, min) {
		right: 14rem;
	}

	@include bp($point_9, $point_2) {
		right: 2rem;
	}
	@include bp($point_2, min) {
		bottom: 3rem;
	}

	@include bp($point_3) {
		bottom: 2.8rem;
		right: 2rem;
		width: 4rem;
		height: 4rem;

		padding: 1.2rem 1rem;

		background-color: $white;
		border-radius: 50%;
		border: 1px solid $black;
	}
}
