@import 'ScssHelpers/helpers-all.scss';

.feature_list__item {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 9.5rem;
		}

		@include bp($point_3) {
			margin-bottom: 6rem;
		}
	}

	&.feature_list__item--img_mod {
		@include bp($point_3) {
			margin-top: -3rem;
		}
	}
}

.feature_list__item_w {
	@include flex_block(row, wrap, flex-start, flex-start);

	.feature_list__item--2_col_mod & {
		@include bp($point_2, min) {
			margin: 0 -4rem -4rem 0;
		}
	}
}

.feature_list__col {
	width: 100%;
	.feature_list__item--2_col_mod & {
		
		@include bp($point_2, min) {
			flex: 1 1 50%;
			padding: 0 4rem 4rem 0;
		}

		@include bp($point_3) {
			flex: 1 1 100%;
			&:not(:last-child) {
				margin-bottom: 6rem;
			}
		}
	}

	.feature_list__item--img_mod & {
		flex: 1 1 100%;
	}
}

.feature_list__text {
	@include text;
}

.feature_list__img_w {
	display: block;
	max-width: 93.8rem;
	width: 100%;
	margin: 0 auto;
}

.feature_list__img {
	display: block;
	width: 100%;
	height: auto;
}