@import 'ScssHelpers/helpers-all.scss';

.download_form__forms_block {
	@include flex_block(row, wrap, flex-start, stretch);
}

.download_form__form_w {
	border-top: 1px solid $black;
	@include bp($point_2, min) {
		padding-top: 2.2rem;
		&:nth-child(1) {
			width: 42.6%;
			padding-right: 11%;
		}

		&:nth-child(2) {
			width: 57.4%;
		}
	}

	@include bp($point_3) {
		width: 100%;
		padding-top: .5rem;
		&:not(:last-child) {
			margin-bottom: 6rem;
		}
	}
}