@mixin form_el_checkbox {
	position: relative;

	padding-left: 3rem;

	font-size: 1.6rem;
	color: $black;
	cursor: pointer;

	.body--black_mod & {
		color: $white;
	}

	.footer__form & {
		font-size: 1.4rem;
		font-weight: 400;
		letter-spacing: 0.01em;

		@include bp($point_2, min) {
			padding-left: 2.3rem;
		}

		@include bp($point_3) {
			padding-left: 2.8rem;
		}
	}

	&:before,
	&:after {
		position: absolute;
		top: 1px;
		left: 0;
		width: 2rem;
		height: 2rem;
		cursor: pointer;
		content: '';

		.footer__form & {
			width: 1.4rem;
			height: 1.4rem;
		}
	}

	&:before {
		// border-radius: 2px;
		border: 1px solid $black;

		transition: transform 0.2s, opacity 0.2s;
		will-change: transform;

		.form_checkbox__element:checked + & {
			opacity: 0;
			transform: rotateZ(40deg) scaleX(0.6) translateY(-3px);
		}

		.body--black_mod & {
			border: 1px solid $white;
			background-color: $white;
		}
	}

	&::after {
		opacity: 0;

		border-bottom: 2px solid $black;
		border-right: 3px solid $black;

		transition: transform 0.2s, opacity 0.2s;

		.body--black_mod & {
			border-bottom: 2px solid $white;
			border-right: 3px solid $white;
		}

		.form_checkbox__element:checked + & {
			transform: rotateZ(40deg) scaleX(0.6) translateY(-3px);
			opacity: 1;
		}
	}

	//--------------------------------------------- Font icon version
	// &:after {
	// 	@include icon-check;
	// 	@include transition_opacity;

	// 	text-align: center;
	// 	line-height: 2rem;
	// 	opacity: 0;
	// 	color: $black;

	// 	.form_checkbox__element:checked + & {
	// 		opacity: 1;
	// 	}
	// }
}

@mixin contact_form_el_checkbox {
	position: relative;

	padding-left: 2.6rem;

	font-weight: 300;
	font-family: $font_1;
	color: $black;
	cursor: pointer;

	@include bp($point_2, min) {
		font-size: 1.6rem;
		line-height: 2rem;
	}

	@include bp($point_3) {
		font-size: 1.2rem;
		line-height: 1.5rem;
		letter-spacing: 0.01em;
	}

	.body--black_mod & {
		color: $white;
	}
}

@mixin contact_form_el_checkbox_decor {
	position: absolute;
	left: 0;

	border-radius: 50%;
	border: 0.1rem solid $black;
	cursor: pointer;

	@include bp($point_11 + 1, min) {
		width: 1.6rem;
		height: 1.6rem;
	}

	@include bp($point_11, $point_1) {
		width: 12px;
		height: 12px;
	}

	@include bp($point_1, $point_2) {
		width: 10px;
		height: 10px;
	}

	@include bp($point_3) {
		width: 1.6rem;
		height: 1.6rem;
	}

	@include bp($point_2, min) {
		top: .1rem;
	}

	@include bp($point_3) {
		top: 0;
	}

	&:after {
		@include transition_opacity;

		content: '';
		position: absolute;
		top: 50%;
		left: 50%;

		transform: translate(-50%, -50%);

		border-radius: 100%;
		background: $black;
		opacity: 0;

		.form_checkbox__element:checked + & {
			opacity: 1;
		}

		@include bp($point_11 + 1, min) {
			width: 0.8rem;
			height: 0.8rem;
		}

		@include bp($point_11, $point_2) {
			width: 6px;
			height: 6px;
		}

		// @include bp($point_1, $point_2 ) {
		// 	width: 5px;
		// 	height: 5px;
		// }

		@include bp($point_3) {
			width: 0.8rem;
			height: 0.8rem;
		}
	}

	.body--black_mod & {
		border: 0.1rem solid $white;
		background-color: $white;
	}
}
