@import 'ScssHelpers/helpers-all.scss';

.description {
	@include bp($point_2, min) {
		padding-top: 3.2rem;
		padding-bottom: 6.4rem;
	}

	@include bp($point_3) {
		padding-top: 0;
	}
}

.description__title {
	@include h1;

	&:not(:last-child) {
		margin-bottom: 2rem;
	}
}

.description__descr {
	@include text;

	@include bp($point_2, min) {
		max-width: 78%;
		margin-bottom: 5.8rem;
	}
}

.description__card {
	@include flex_block(row, wrap, flex-start, flex-start, flex-start);

	@include bp($point_2, min) {
		margin-bottom: 8.4rem;
	}
}

.description__card_col {
	&:nth-child(1) {
		width: 74.4%;
	}

	&:nth-child(2) {
		width: 25.6%;
	}
}

.description__card_picture {
	display: block;
	width: 100%;
	height: 100%;
}

.description__card_img {
	@include cover_img;
}

//--------------------------- description card info

.description__card_info {
	padding: 16.2rem 0 0 18rem;
}

.description__card_info_list {
	position: relative;
	@include bp($point_2, min) {
		margin-bottom: 7.4rem;
	}
}

.description__card_info_list_item {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 3.1rem;
		}
	}
}

.description__card_info_title,
.description__card_info_descr {
	font-size: 2rem;
}

.description__card_info_title {
	font-weight: 400;
}

.description__card_info_descr {
	font-weight: 300;
}

//--------------------------- description btns

.description__card_info_btns_wrap {
	@include flex_block(row, wrap, flex-start, center, flex-start);

	@include bp($point_2, min) {
		margin-bottom: 7.1rem;
	}
}

.description__card_info_btns {
	@include flex_block(row, wrap, flex-start, center, flex-start);

	@include bp($point_2, min) {
		margin-right: 6rem;
	}
}

.description__card_info_btn {
	display: block;

	width: 4rem;
	height: 4rem;

	color: $black;

	border: 1px solid $black;
	border-radius: 50%;

	@include flex_block(row, wrap, center, center, center);

	&:not(:last-child) {
		margin-right: 1.8rem;
	}
}

.description__card_info_btn_in {
	width: 1.6rem;
	height: 1.6rem;
}

.description__card_info_link {
	font-size: 1.4rem;
	color: $black;

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

//--------------------------- description cta btn

.description__cta_btn_wrap {
}

.description__cta_btn {
	padding: 0 4.6rem !important;

	@include btn_size_normal;
	@include btn_style_white_border;
}

.description__cta_btn_icon {
	width: 2rem;
	height: 1.6rem;

	margin-right: 2rem;
}
