@mixin form_el_label {
	display: block;
	margin-bottom: 0.4rem;

	font-size: 1.6rem;
	color: $black;
}

@mixin form_el_default {
	display: block;
	width: 100%;
	height: $input_height;
	padding: $input_offset;
	font-family: $font_1;
	color: $input_text_color;
	border: none;
	border-bottom: 1px solid $black;
	border-radius: $input_radius;
	background: transparent;

	@include bp($point_2, min) {
		font-size: $input_font_size;
		letter-spacing: 0.16px;
	}

	@include bp($point_3) {
		font-size: 1.4rem;
	}

	&::-webkit-input-placeholder {
		color: $black;
	}

	&::-moz-placeholder {
		color: $black;
		opacity: 1;
	}

	&:-ms-input-placeholder {
		color: $black;
	}

	// &:hover,
	// &:focus {
	// 	box-shadow: inset 0 0 0 1px $input_border_color_active;
	// }
}

@mixin form_el_email {
	display: block;
	width: 100%;
	padding: 0;
	height: 3.4rem;

	font-size: 1.6rem;
	letter-spacing: 0.01em;
	font-weight: 400;
	font-family: $font_1;
	color: $black;
	background-color: transparent;
	border: none;
	background-clip: padding-box;

	border-bottom: 1px solid $black;

	transition: border 0.3s ease;

	.body--black_mod & {
		color: $white;
		border-bottom: 1px solid $white;
	}

	// &:hover {
	// 	border-bottom: 1px solid $gray_3;
	// }

	&::-webkit-input-placeholder {
		color: $black;

		.body--black_mod & {
			color: $white;
		}
	}
	&::-moz-placeholder,
	&:-moz-placeholder {
		color: $black;
		opacity: 1;

		.body--black_mod & {
			color: $white;
		}
	}
	&:-ms-input-placeholder {
		color: $black;

		.body--black_mod & {
			color: $white;
		}
	}

	/* clears the 'X' from IE */
	&::-ms-clear,
	&::-ms-reveal {
		display: none;
		width: 0;
		height: 0;
	}
	/* clears the 'X' from Chrome */
	&::-webkit-search-decoration,
	&::-webkit-search-cancel-button,
	&::-webkit-search-results-button,
	&::-webkit-search-results-decoration {
		display: none;
	}

	@include bp($point_2, min) {
		padding: 0 0 1.2rem 0;
	}
	@include bp($point_3) {
	}
}

@mixin contact_form_el {
	display: block;
	width: 100%;
	padding: 0;
	font-size: 1.6rem;
	font-weight: 300;
	font-family: $font_1;
	color: $black;
	background-color: transparent;
	border: none;
	background-clip: padding-box;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;

	box-shadow: inset 0 -1px 0 0 $black;

	transition: box-shadow 0.3s ease;

	@include bp($point_2, min) {
		height: 5.5rem;
	}

	@include bp($point_3) {
		height: 3.7rem;
	}

	&:focus {
		box-shadow: inset 0 -1px 0 0 rgba($black, 0.3);
	}

	&::-webkit-input-placeholder {
		color: $black;
	}
	&::-moz-placeholder,
	&:-moz-placeholder {
		color: $black;
		opacity: 1;
	}
	&:-ms-input-placeholder {
		color: $black;
	}

	.form_sec_in--color_mod & {
		color: $white;
		box-shadow: inset 0 -1px 0 0 $white;
	}

	.form_sec_in--color_mod &::placeholder {
		color: $white;
	}

	.form_sec_in--color_mod &::-moz-placeholder,
	.form_sec_in--color_mod &:-moz-placeholder {
		color: $white;
		opacity: 1;
	}

	.form_sec_in--color_mod &:-ms-input-placeholder {
		color: $white;
	}
}
