@import 'ScssHelpers/helpers-all.scss';

.designer_list {
	@include bp($point_2, min) {
		padding-bottom: 14rem;
	}

	@include bp($point_3) {
		padding-top: 3.6rem;
		padding-bottom: 2rem;
	}
}

.designer_list__list {
	@include bp($point_2, min) {
		margin: 0 -4rem -3rem 0;
	}

	@include bp($point_3) {
		margin: 0 -2rem -3.6rem 0;
	}

	@include flex_block(row, wrap, flex-start, flex-start, flex-start);
}

.designer_list__list_item {
	@include bp($point_2, min) {
		width: 25%;

		padding: 0 4rem 3rem 0;
	}

	@include bp($point_3) {
		width: 50%;

		padding: 0 2rem 3.6rem 0;
	}
}

.designer_list__list_link {
	display: block;
	color: $black;

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

.designer_list__list_link_picture {
	position: relative;
	display: block;

	width: 100%;
	padding-top: 100%;

	overflow: hidden;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.2rem;
		}

		@include bp($point_3) {
			margin-bottom: 1rem;
		}
	}
}

.designer_list__list_link_img {
	position: absolute;
	top: 0;
	left: 0;

	@include cover_img;
	@include hover_img('.designer_list__list_link');
}

.designer_list__list_link_title {
	@include text_2;
}
