@import 'ScssHelpers/helpers-all.scss';

.products__section {
	// .section_head {
	// 	margin-bottom: 4.6rem;
	// }

	@include bp($point_3) {
		&.products__section--no_offset_mod {
			padding-top: 0;
		}
	}
}

.products__section_head {
	margin-bottom: 8.8rem;
}

.products__section_title {
	@include h1;

	&:not(:last-child) {
		margin-bottom: 2.2rem;
	}
}

.products__section_text {
	@include text;
}

.products__list_wrap {
	position: relative;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 9.8rem;
		}

		@include bp($point_3) {
			margin-bottom: 3.6rem;
		}
	}
}

.products__list {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -4rem -3.2rem 0;
	overflow: visible;

	&.products__list--height_1_mod {
		@include bp($point_2, min) {
			margin: 0 -4rem -4rem 0;
		}
	}

	.products__list_wrap--slider_mod & {
		overflow: visible;
		@include bp($point_3) {
			margin: 0;
		}
	}
}

.products {
	width: 25%;
	padding: 0 4rem 3.2rem 0;

	&.swiper-slide {
		width: 25%;
	}

	.products__list--height_1_mod & {
		padding: 0 4rem 4rem 0;
	}

	@include bp($point_2, min) {
		.products__list--v1_mod & {
			&:nth-child(10n + 2),
			&:nth-child(10n + 3),
			&:nth-child(10n + 5),
			&:nth-child(10n + 6),
			&:nth-child(10n + 7),
			&:nth-child(10n + 8),
			&:nth-child(10n + 9),
			&:nth-child(10n + 10) {
				width: 16.66%;
			}

			&:nth-child(10n + 7) {
				width: 33.33%;
			}
		}

		.products__list--v2_mod & {
			&:nth-child(19n + 3),
			&:nth-child(19n + 7),
			&:nth-child(19n + 8),
			&:nth-child(19n + 9),
			&:nth-child(19n + 10),
			&:nth-child(19n + 11),
			&:nth-child(19n + 12),
			&:nth-child(19n + 16),
			&:nth-child(19n + 17),
			&:nth-child(19n + 18) {
				width: 16.66%;
			}

			&:nth-child(19n + 4) {
				width: 33.33%;
			}
		}

		.products__list--v3_mod & {
			&:nth-child(14n + 3),
			&:nth-child(14n + 4),
			&:nth-child(14n + 5),
			&:nth-child(14n + 6),
			&:nth-child(14n + 7),
			&:nth-child(14n + 8),
			&:nth-child(14n + 13) {
				width: 16.66%;
			}

			&:nth-child(14n + 14) {
				width: 33.33%;
			}
		}

		.products__list--v4_mod & {
			&:nth-child(7n + 4) {
				width: 16.66%;
			}

			&:nth-child(7n + 1),
			&:nth-child(7n + 7) {
				width: 33.33%;
			}

			&:nth-child(7n + 3) {
				width: 41.67%;
			}
		}

		.products__list--v5_mod & {
			&:nth-child(9n + 2),
			&:nth-child(9n + 3),
			&:nth-child(9n + 5),
			&:nth-child(9n + 7),
			&:nth-child(9n + 9) {
				width: 16.66%;
			}

			&:nth-child(9n + 6),
			&:nth-child(9n + 8) {
				width: 33.33%;
			}
		}
	}

	.products__list--slider_mod & {
		width: 42rem;

		&:nth-child(5n + 3) {
			width: 28rem;
		}

		&:nth-child(5n + 4) {
			width: 56rem;
		}
	}

	.products__list--slider_2_mod & {
		&:nth-child(5n + 1),
		&:nth-child(5n + 2),
		&:nth-child(5n + 3),
		&:nth-child(5n + 5) {
			width: 28rem;
		}

		&:nth-child(5n + 4) {
			width: 36.6rem;
		}
	}

	.products__list--slider_3_mod & {
		&:nth-child(3n + 1) {
			width: 28rem;
		}

		&:nth-child(3n + 2) {
			width: 42rem;
		}

		&:nth-child(3n + 3) {
			width: 56rem;
		}
	}

	.products__list_wrap:not(.products__list_wrap--slider_mod) & {
		@include bp($point_3, $point_4) {
			width: 33.33%;
		}

		@include bp($point_4 - 1) {
			width: 100%;
		}
	}

	.products__list_wrap--slider_mod & {
		@include bp($point_3) {
			padding: 0;
			width: auto !important;

			&:not(:last-child) {
				margin-right: 2rem;
			}
		}
	}
}

.products__link {
	display: block;
	color: $black;

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

.products__pic_wrap {
	display: flex;
	align-items: center;
	justify-content: center;
	margin-bottom: 1.2rem;

	background-color: $gray;

	@include bp($point_4, min) {
		height: 28.4rem;
	}

	.products__list_wrap:not(.products__list_wrap--slider_mod) & {
		@include bp($point_4 - 1) {
			height: auto;
			min-height: 26.2rem;
		}
	}

	.products__list_wrap.products__list_wrap--slider_mod & {
		@include bp($point_2, min) {
			height: 28.4rem;
		}

		@include bp($point_3) {
			height: 17rem;
			min-height: 0;
		}
	}

	.products__list.products__list--height_1_mod & {
		@include bp($point_2, min) {
			height: 31.8rem;
		}
	}

	.products__list.products__list--height_2_mod & {
		@include bp($point_2, min) {
			height: 18rem;
		}

		@include bp($point_3) {
			height: 17rem;
		}
	}

	.products__list_wrap--slider_mod & {
		@include bp($point_3) {
			height: 17rem;
		}
	}

	.products.products--not_product_mod & {
		min-height: 0;
	}
}

.products__pic {
	display: block;
	width: 100%;
	height: 100%;
	overflow: hidden;

	.products__list_wrap--slider_mod & {
		@include bp($point_3) {
			width: auto;
		}
	}
}

.products__img {
	@include contain_img;
	@include hover_img('.products__link');

	.products--not_product_mod & {
		object-fit: cover;
	}

	.products__list_wrap--slider_mod & {
		@include bp($point_3) {
			width: auto;
		}
	}
}

.products__title {
	@include text_2;
}

.products__btn_wrap {
	display: flex;
	justify-content: center;
}

.products__btn {
	@include btn_size_small;
	@include btn_style_white_border;

	padding: 0 3rem;
}
