@import 'ScssHelpers/helpers-all.scss';

.services {
}

.services__list {
	padding-left: 2rem;

	border-left: 1px solid $black;

	.body--black_mod & {
		border-left: 1px solid $white;
	}
}

.services__item {
	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, flex-start, flex-start);

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 5.6rem;
		}
	}
}

.services__item__col {
	&:nth-child(2n + 1) {
		@include bp($point_2, min) {
			width: 61.6%;
			flex-shrink: 0;

			padding-top: 2px;

			&:not(:last-child) {
				padding-right: 21.8rem;
			}
		}
		@include bp($point_3) {
			&:not(:last-child) {
				margin-bottom: 1.6rem;
			}
		}
	}

	&:nth-child(2n + 2) {
		position: relative;

		&::after {
			content: '';
			display: block;
			padding-bottom: 66.6%;
		}

		@include bp($point_2, min) {
			width: 38.4%;
			flex-shrink: 0;

			margin-left: auto;
		}
		@include bp($point_3) {
			max-width: 39rem;
			width: 100%;
			margin: 0 auto;
		}
	}
}

.services__pic {
	@include stretch;
}

.services__img {
	@include cover_img;
}
