@import 'family';
@import 'reset';
@import 'fonts';
@import 'print';

//------------------------------------------------------------base styles
:root {
	--full-screen: calc(var(--vh, 1vh) * 100);

	--height-footer: 30rem;

	--plyr-color-main: transparent;

	--section-text-color: $black;

	@include bp($point_2, min) {
		--gl-indent: 20px;
		--height-header: 8rem;
		--plyr-control-icon-size: 10rem;
	}

	@include bp($point_3) {
		--gl-indent: 2rem;
		--height-header: 5.4rem;
		--plyr-control-icon-size: 3.4rem;
	}
}

html {
	height: 100%;

	font-weight: 400;
	line-height: 1.33;
	font-family: $font_1;

	@include bp($point_9 + 1, min) {
		font-size: calc(100vw / 1920 * 10);
	}

	@include bp($point_9, $point_2) {
		font-size: calc(100vw / 1440 * 10);
	}

	@include bp($point_3, 390px) {
		font-size: 10px;
	}

	@include bp(389px) {
		font-size: calc(100vw / 390 * 10);
	}

	@include bp($point_3) {
		position: fixed;
		left: 0;
		top: 0;
		height: 100% !important;
		width: 100%;
		overflow: hidden;
	}
}

body {
	position: relative;

	display: flex;
	height: 100%;

	font-size: $font_size_base;
	color: $color_text_base;

	-webkit-text-size-adjust: none;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background: $body_bg;

	&.body--open_menu_state,
	&.body--popup_open {
		// position: fixed;
		// overflow: hidden !important;
	}

	@supports (display: grid) {
		display: block;
	}

	@include bp($point_3) {
		position: fixed;
		left: 0;
		top: 0;
		height: 100% !important;
		width: 100%;
		overflow: hidden;
	}
}

.body--black_mod {
	background: $black;
	--section-text-color: $white;
}

.preloader {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background-color: $white;

	opacity: 1;
	transition: opacity 0.3s;

	z-index: 1000;

	.body--loaded & {
		pointer-events: none;
		opacity: 0;
	}
}

.ScrollSmoother-wrapper {
	@include bp($point_3) {
		overflow-y: auto !important;
	}
}

.ScrollSmoother-wrapper,
.wrapper {
	@include bp($point_3) {
		height: 100% !important;
	}
}

.wrapper {
	position: relative;

	width: 100%;
	display: flex;
	flex-direction: column;
	min-height: 100%;

	overflow-x: hidden !important;
	will-change: transform;

	@include bp($point_2, min) {
		height: auto !important;
	}
}

.base {
	width: 100%;
	position: relative;
	padding: var(--height-header) 0 0;

	flex: 1 0 auto;
	overflow: hidden;
}

a {
	text-decoration: none;
	color: $color_link_base;

	&:hover,
	&:focus {
		text-decoration: underline;
	}
}

blockquote {
	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 1.8rem;
		}
	}
}

blockquote p {
	font-style: italic;
	line-height: 1.4;
	font-family: $modern;

	@include bp($point_4, min) {
		font-size: 2.6rem;
		font-weight: 500;

		.hero_v4 & {
			font-size: 2.2rem;
		}
	}

	@include bp($point_4 - 1) {
		font-size: 1.8rem;
		font-weight: 300;
	}
}

figcaption {
	font-size: 1.4rem;
	font-weight: 300;
	line-height: 1.4;
	letter-spacing: 0.01em;
}

