.hero_v6__list {
	@include bp($point_4, min) {
		@include flex_block(row, wrap, flex-start, flex-start);
	}
}

.hero_v6__item {
	&:nth-child(3n + 1) {
		@include bp($point_4, min) {
			&:not(:last-child) {
				margin-right: 1rem;
			}
		}
	}

	&:nth-child(3n + 2) {
		margin-left: auto;

		@include bp($point_2, min) {
			margin-top: 25.4rem;
		}

		@include bp($point_3, $point_4) {
			margin-top: 10rem;
		}
	}

	&:nth-child(3n + 3) {
		@include bp($point_2, min) {
			margin-top: 9.4em;
			margin-left: 42rem;
		}

		@include bp($point_3, $point_4) {
			margin-top: 6em;
			margin-left: auto;
			margin-right: auto;
		}
	}

	@include bp($point_4, min) {
		width: 40.2%;
		flex-shrink: 0;
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 5.4rem;
		}
	}
}

.hero_v6__item_in {
	will-change: transform;
}

.hero_v6__item_heading {
	font-weight: 300;
	line-height: 1.36;
	font-family: $modern;
	font-style: italic;

	&:not(:last-child) {
		margin-bottom: 0.8rem;
	}

	@include bp($point_2, min) {
		font-size: 2.2rem;
	}

	@include bp($point_3) {
		font-size: 1.8rem;
	}
}

.hero_v6__pic {
	position: relative;
	display: block;

	padding-bottom: calc(434 / 660 * 100%);

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 2.6rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.hero_v6__img {
	position: absolute;
	top: 0;
	left: 0;

	@include cover_img;
}

.hero_v6__item_text {
	@include text;
}
