@import 'ScssHelpers/helpers-all.scss';

.search_hero {
	padding-bottom: 1rem;
}

.search_hero__cols {
	display: flex;
	flex-wrap: wrap;
}

.search_hero__col {
	&:nth-child(1) {
		@include bp($point_4, min) {
			width: 51%;
			padding-right: 4rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 3rem;
		}
	}

	&:nth-child(2) {
		@include bp($point_4, min) {
			width: 49%;
			padding-top: 1.8rem;
		}
	}

	@include bp($point_4 - 1) {
		width: 100%;
	}
}

.search_hero__title {
	@include h1;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2rem;
		}

		@include bp($point_3) {
			margin-bottom: 1rem;
		}
	}
}

.search_hero__results {
	@include text;

	display: flex;
	align-items: center;
}

.search_hero__results_title {
	margin-right: 0.4rem;
}

.search_hero__form {
	position: relative;
	display: block;
	width: 100%;
}

.search_hero__submit {
	position: absolute;
	top: 50%;
	right: 0;

	width: 2rem;
	height: 2rem;
	margin-top: -1.2rem;

	color: $black;
}
