.hero_v2 {
	&.hero_v2--indent_mod {
		@include bp($point_2, min) {
			padding-bottom: 2rem;
		}
	}
}

.hero_v2__in {
	@include bp($point_5, min) {
		&:not(:last-child) {
			margin-bottom: 6.8rem;
		}
	}

	@include bp($point_5 - 1) {
		&:not(:last-child) {
			margin-bottom: 2.8rem;
		}
	}
}

.hero_v2__title {
	@include h1;

	&:not(:last-child) {
		margin-bottom: 2.2rem;
	}
}

.hero_v2__text {
	@include text;
}

.hero_v2__pic {
	position: relative;
	display: block;
	margin: 0 calc(var(--gl-indent) * -1);
	overflow: hidden;

	@include bp($point_5, min) {
		padding-bottom: 35.4%;
	}

	@include bp($point_5 - 1) {
		padding-bottom: 116.6%;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 9.8rem;
		}

		@include bp($point_2) {
			margin-bottom: 5.8rem;
		}
	}
}

.hero_v2__img {
	position: absolute;
	left: 0;
	bottom: 0;

	@include cover_img;

	height: 120% !important;
}

.hero_v2__btn {
	@include bp($point_4 - 1) {
		position: absolute;
		bottom: 5.8rem;
		right: 2rem;
	}
}
