//------------------------------------------------------------layout

// .widget_wrap {
// 	display: none;
// }

.header {
	position: fixed;
	top: 0;
	left: 0;

	display: flex;
	align-items: center;
	height: var(--height-header);
	width: 100%;
	padding: 1rem var(--gl-indent);

	font-size: 1rem;

	will-change: transform;

	@include z-index(header);

	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;

		width: 100%;
		height: 100%;

		background-color: $white;
		box-shadow: 0 0 10px rgba(0, 0, 0, 0.16);
		z-index: -1;
	}
}

.header_in {
	display: flex;
	align-items: center;
}

.header_logo {
	@include bp($point_2, min) {
		position: absolute;
		top: 50%;
		left: 50%;

		width: 22rem;
		height: 2.2rem;
		margin-left: -11rem;
		margin-top: -1.1rem;
	}

	@include bp($point_3) {
		width: 17.2rem;
		height: 1.8rem;
		margin-right: 3rem;
	}
}

.header_logo__img {
	@include contain_img;
}

.nav {
	display: flex;

	@include bp($point_2, min) {
		align-items: center;
		flex-grow: 1;
	}

	@include bp($point_3) {
		position: fixed;
		top: var(--height-header);
		left: 0;

		flex-direction: column;
		width: 100%;
		height: calc(var(--full-screen) - var(--height-header));
		padding: 3.6rem var(--gl-indent);

		background-color: $beige;

		overflow-y: auto;
		transform: translateY(-1rem);
		opacity: 0;
		pointer-events: none;

		transition: opacity 0.3s, transform 0.3s;

		z-index: -3;

		.body--open_menu_state & {
			opacity: 1;
			transform: translateY(0);
			pointer-events: initial;
		}
	}
}

.nav__block_wrap {
	@include bp($point_2, min) {
		display: flex;
		flex-grow: 1;
	}

	@include bp($point_3) {
		margin-bottom: 4rem;
	}
}

.nav__block {
	@include bp($point_2, min) {
		display: flex;
		align-items: center;

		&:first-child {
			margin-right: auto;
		}
	}

	@include bp($point_3) {
		margin-bottom: 0.7rem;
	}
}

.main_menu__list {
	@include bp($point_2, min) {
		display: flex;
		flex-grow: 1;
		margin-right: 2rem;
	}
}

.main_menu__item {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-right: 2.1rem;
		}

		@include bp($point_3) {
			margin-bottom: 0.7rem;
		}
	}

	// &:nth-child(6) {
	// 	margin-left: auto;
	// }
}

.main_menu__link {
	position: relative;

	display: block;

	color: $black;

	@include bp($point_2, min) {
		font-size: 1.6rem;
		letter-spacing: 0.16px;
	}

	@include bp($point_3) {
		font-size: 2.2rem;
	}

	&.main_menu__link--submenu_mod {
		user-select: none;
		&:before {
			@include bp($point_3) {
				top: 50%;
				right: 0;

				width: 1.2rem;
				height: 0.6rem;
				margin-top: -0.3rem;

				background-color: tomato;
				background: url('../images/icons/other-icons/caret.svg') no-repeat center center;
				background-size: 100% 100%;

				transform-origin: 50% 50%;
				transition: transform 0.3s;

				.main_menu__item--active_state & {
					transform: rotate(180deg);
				}
			}
		}
	}

	&:before {
		content: '';
		position: absolute;

		@include bp($point_2, min) {
			top: 100%;
			left: 50%;

			width: calc(100% + 3rem);
			height: 3rem;

			transform: translateX(-50%);
			pointer-events: none;

			.main_menu__item:hover &,
			.main_menu__item:focus & {
				pointer-events: initial;
			}
		}
	}

	@include hover_link;
}

.header_links {
	display: flex;
	align-items: center;

	@include bp($point_3) {
		margin: 0.4rem 2rem 0 auto;
	}
}

.header_links__item {
	&:not(:last-child) {
		margin-right: 2rem;
	}
}

.header_links__el {
	display: block;

	color: $black;
	transition: opacity 0.3s;

	@include bp($point_2, min) {
		width: 1.3rem;
		height: 1.3rem;
	}

	@include bp($point_3) {
		width: 2.2rem;
		height: 2.2rem;
	}

	&:hover,
	&:focus {
		opacity: 0.5;
	}

	&.header_links__el--favourite_mod {
		width: 1.8rem;
		height: 1.5rem;

		@include bp($point_3) {
			width: 2.6rem;
			height: 2.2rem;
		}
	}
}

.submenu {
	@include bp($point_2, min) {
		position: absolute;
		top: 100%;
		left: 0;

		display: flex;
		justify-content: center;
		align-items: center;
		min-height: 7rem;
		width: 100%;
		padding: 1rem var(--gl-indent);

		background-color: $white;
		min-height: 7rem;
		box-shadow: 0 0 10px rgba(0, 0, 0, 0.16);

		opacity: 0;
		transform: translateY(-1rem);
		pointer-events: none;
		transition: opacity 0.3s, transform 0.3s;

		z-index: -3;
	}

	@include bp($point_3) {
		display: none;
		padding: 1.4rem 0 1rem;
	}

	.main_menu__item--active_state & {
		@include bp($point_3) {
			display: block;
		}
	}

	.main_menu__item:hover & {
		opacity: 1;
		transform: translateY(0);
		pointer-events: initial;
	}
}

.submenu__list_wrap {
	&:not(:last-child) {
		margin-right: 2.4rem;
	}
}

.submenu__list {
	margin: 0 -2.4rem -1rem 0;

	@include bp($point_2, min) {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
	}

	@include bp($point_3) {
		column-count: 2;
		margin: 0 -1rem -1rem 0;
	}
}

.submenu__item {
	padding: 0 2.4rem 1rem 0;

	&.submenu__item--btn_mod {
		@include bp($point_2, min) {
			display: none;
		}
	}

	@include bp($point_3) {
		// width: 50%;
		padding: 0 1rem 1rem 0;
	}
}

.submenu__link {
	display: block;

	color: $black;

	@include bp($point_2, min) {
		font-size: 1.6rem;
		letter-spacing: 0.16px;
	}

	@include bp($point_3) {
		font-size: 1.4rem;
	}
}

.submenu__btn_wrap {
	@include bp($point_3) {
		display: none;
	}
}

.submenu__btn {
	@include btn_size_small;
	@include btn_style_white_border;
}

// nav bottom

.nav__bottom {
	margin-top: auto;

	@include bp($point_2, min) {
		display: none;
	}
}

.nav__menu_wrap {
	margin-bottom: 2.8rem;
}

.nav__menu {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -1rem -0.7rem 0;
}

.nav__menu_item {
	position: relative;
	padding: 0 1rem 0.7rem 0;

	&:nth-child(odd) {
		width: 51%;
	}

	&:nth-child(even) {
		width: 49%;
	}
}

.nav__menu_link {
	display: block;

	font-size: 1.4rem;
	color: $black;

	&:hover,
	&:focus {
		text-decoration: none;
	}

	&.nav__menu_link--list_mod {
		position: relative;
		padding-right: 2rem;

		&:before {
			content: '';
			position: absolute;
			top: 50%;
			right: 0;

			width: 1.2rem;
			height: 0.6rem;
			margin-top: -0.3rem;

			background: url('../images/icons/other-icons/caret.svg') no-repeat center center;
			background-size: 100% 100%;
		}
	}
}

.nav__menu_submenu {
	position: absolute;
	bottom: calc(100% + 0.6rem);
	left: 0;

	width: 100%;
	background-color: $white;
	box-shadow: 0 0 3px rgba($black, 0.3);
	padding: 1rem;

	opacity: 0;
	pointer-events: none;
	transform: translateY(1rem);
	transition: transform 0.3s, opacity 0.3s;

	&.nav__menu_submenu--active_state {
		opacity: 1;
		pointer-events: initial;
		transform: translateY(0);
	}
}

.nav__menu_submenu_link {
	font-size: 1.4rem;
	color: $black;
}

.nav__social {
	display: flex;
	margin-left: -0.2rem;
}

.nav__social_item {
	&:not(:last-child) {
		margin-right: 1.6rem;
	}
}

.nav__social__link {
	display: flex;
	align-items: center;
	justify-content: center;

	width: 1.5rem;
	height: 1.8rem;
	color: $black;
}

.header_search {
	position: absolute;
	top: 100%;
	left: 0;

	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 7rem;
	width: 100%;
	padding: 1rem var(--gl-indent);

	background-color: $white;
	min-height: 7rem;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.16);

	opacity: 0;
	transform: translateY(-1rem);
	pointer-events: none;
	transition: opacity 0.3s, transform 0.3s;

	z-index: -2;

	&.header_search--active_state {
		opacity: 1;
		pointer-events: initial;
		transform: translateY(0);
	}
}

.header_search__form {
	position: relative;

	display: block;
	width: 100%;
	max-width: 80rem;
}

.header_search__submit {
	position: absolute;
	top: 50%;
	right: 0;

	width: 1.6rem;
	height: 1.6rem;
	margin-top: -0.8rem;

	color: $black;

	cursor: pointer;
}

//------------------------------------------------------------layout###

@mixin user_menu_butt_line {
	position: absolute;
	top: 50%;
	left: 0;
	width: 100%;
	height: 2px;
	margin-top: -0.2rem;
	background: black;
}

.menu_trigger {
	z-index: 210;
	position: relative;
	display: none; // hidden on wide screens
	height: 0.8rem;
	width: 2.6rem;
	margin-top: 0.5rem;
	text-transform: uppercase;

	color: transparent;
	cursor: pointer;

	@include bp($point_3) {
		display: block; // appear on tablets
	}

	&:before {
		content: '';
		transform: translate(0, -0.7rem);
		transition: all 0.3s ease;

		@include user_menu_butt_line;
	}
	&:after {
		content: '';
		transform: translate(0, 0.7rem);
		transition: all 0.3s ease;

		@include user_menu_butt_line;
	}
	.body--open_menu_state & {
		&:before {
			transform: (rotate(45deg) translate(0, 0));
		}

		&:after {
			transform: (rotate(-45deg) translate(0, 0));
		}
	}
}

.menu_trigger_decor {
	transition: all 0.3s ease;

	.body--open_menu_state & {
		opacity: 0;
		transform: translate(100%, 0);
	}

	@include user_menu_butt_line;
}
