@import 'ScssHelpers/helpers-all.scss';

.hero_v3__wrap {
	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, flex-start);
	}
}

.hero_v3__col {
	&:not(:last-child) {
		margin-bottom: 2rem;
	}

	&:nth-child(2n + 1) {
		@include bp($point_2, min) {
			width: 51.2%;
		}
	}

	&:nth-child(2n + 2) {
		@include bp($point_2, min) {
			width: 48.8%;
			padding-top: 20.6rem;
		}
	}

	@include bp($point_2, min) {
		flex-shrink: 0;

		&:not(:last-child) {
			padding-right: 18.2rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 6rem;
		}
	}
}

.hero_v3__img_in {
	position: relative;
	display: block;
	width: 100%;

	&:not(:last-child) {
		margin-bottom: 2rem;
	}

	.hero_v3__col:nth-child(2n + 2) & {
		@include bp($point_2, min) {
			padding-top: calc(501 / 800 * 100%);
		}
	}

	.hero_v3__col:nth-child(2n + 1) & {
		@include bp($point_2, min) {
			padding-top: calc(823 / 658 * 100%);
		}
	}

	@include bp($point_3) {
		padding-top: calc(232 / 350 * 100%);
	}
}

.hero_v3__img {
	@include cover_img;

	position: absolute;
	top: 0;
	left: 0;
}

.hero_v3__text {
	@include text;

	@include bp($point_2, min) {
		p {
			&:not(:last-child) {
				margin-bottom: 2.8rem;
			}
		}

		&:not(:last-child) {
			margin-bottom: 19rem;
		}
	}

	@include bp($point_3) {
		p {
			&:not(:last-child) {
				margin-bottom: 2.2rem;
			}
		}
		&:not(:last-child) {
			margin-bottom: 5.8rem;
		}
	}
}
