@import 'ScssHelpers/helpers-all.scss';

.download {
	&.download--v2-mod {
		@include flex_block(column, wrap, flex-start, stretch, flex-start);
	}
}

.download__head {
	position: relative;

	&::after {
		position: absolute;
		top: 0;
		left: 0;

		content: '';

		width: 100%;
		height: 1px;

		background-color: $black;
	}

	@include bp($point_4, min) {
		padding-top: 2.2rem;

		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}

	@include bp($point_4 - 1) {
		padding-top: 0.8rem;

		&:not(:last-child) {
			margin-bottom: 2.6rem;
		}
	}
}

.download__title {
	@include h2;
}

.download__wrap {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 5.2rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}

	.download--v2-mod & {
		order: 3;

		&:not(:last-child) {
			margin-bottom: 0;
		}
	}
}

.download__list {
	@include bp($point_2, min) {
		margin: 0 -2.6rem -2.6rem 0;

		@include flex_block(row, wrap, flex-start, center);
	}

	@include bp($point_3) {
		@include flex_block(column, nowrap, flex-start, stretch);
	}
}

.download__item {
	flex-shrink: 0;

	@include bp($point_2, min) {
		padding: 0 2.6rem 2.6rem 0;
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}
}

.download__btn {
	width: 100%;
	@include flex_block(row, nowrap, center, center);
}

.download__link_icon {
	//width: 1.6rem;
	//height: 1.8rem;
	width: 2.6rem;
	height: 2.6rem;

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-right: 0.8rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-right: 1rem;
		}
	}
}

.download__link {
	@include text_2;

	font-weight: 500;
	line-height: 1;
	color: $black;
	font-family: $modern;

	@include bp($point_4 - 1) {
		padding-top: 3px;
	}
}

.download__items {
	.download--v2-mod & {
		order: 2;

		margin-bottom: 2.6rem;
	}
}

.download__text {
	@include text;

	@include bp($point_3) {
		margin-bottom: 2.2rem;
	}
}
