@import 'ScssHelpers/helpers-all.scss';

.filter {
	&.filter--indent_mod_1 {
		@include bp($point_2, min) {
			padding-bottom: 9rem;
		}

		@include bp($point_3) {
			margin-bottom: 3rem;
		}
	}

	&.filter--indent_mod_2 {
		padding-bottom: 5.6rem;
	}

	&.filter--indent_mod_3 {
		padding-bottom: 0;
	}

	&.filter--position_mod_1 {
		padding-top: 1.4rem;
		padding-bottom: 5rem;
	}
}

.filter__wrap {
	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start);
	}

	&:not(.popup .filter__wrap) {
		@include bp($point_3) {
			display: none;
		}
	}
}

.filter__form {
	position: relative;
	@include bp($point_2, min) {
		width: 31.61%;
	}

	& .form_input {
		width: 100%;
	}

	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 2rem;
		}
	}
}

.filter__search {
	width: 100%;
	padding: 1rem 0;

	font-size: 1.6rem;
	font-family: $font_1;
	letter-spacing: 0.02em;

	border: none;
	border-bottom: 1px solid $black;

	background-color: transparent;

	&::placeholder {
		color: $black;
	}
}

.filter__btn_submit {
	position: absolute;
	top: 50%;
	right: 0;

	transform: translateY(-50%);
}

.filter__icon_search {
	width: 2.1rem;
	height: 2.1rem;
}

.filter__selects_wrap {
	&:not(:first-child) {
		@include bp($point_2, min) {
			padding-left: 4rem;
		}
	}

	@include bp($point_2, min) {
		width: 68.38%;
	}

	.filter--position_mod_1 & {
		@include bp($point_2, min) {
			width: 100%;
		}
	}
}

.filter_list {
	@include flex_block(row, wrap, flex-start);

	margin: 0 -4.2rem -4rem 0;
}

.filter_list__item {
	width: 25%;
	padding: 0 4.2rem 4rem 0;

	&:first-child {
		margin-left: auto;
	}

	.filter--position_mod_1 & {
		max-width: 26rem;

		&:first-child {
			margin-left: 0;
		}

		&:last-child {
			margin-left: auto;
		}
	}
}

.filter_select {
	position: relative;

	width: 100%;

	@include bp($point_2, min) {
		padding: 1rem 0;
	}

	@include bp($point_3) {
		padding-bottom: 1rem;
	}
}

.filter_select__title {
	position: relative;

	line-height: 1.15;

	color: $black;

	cursor: pointer;

	@include bp($point_2, min) {
		font-size: 1.6rem;
		letter-spacing: 0.02em;
	}

	@include bp($point_3) {
		font-size: 1.8rem;
		margin-bottom: 1rem;
	}
}

.filter_select__arrow_select {
	@include transition_transform;

	position: absolute;
	top: 3px;
	right: 3px;

	width: 1.2rem;
	height: 1.2rem;

	pointer-events: none;

	.filter_select--open_state & {
		transform: rotate(-180deg);
	}
}

.filter_select__menu {
	width: 100%;
	overflow: hidden;

	@include bp($point_2, min) {
		position: absolute;

		z-index: 5;
		top: 3.9rem;
		left: 0;
	}

	@include bp($point_3) {
		max-height: 0;
		height: 100%;
		border-bottom: 1px solid $black;

		transition: max-height 0.5s cubic-bezier(0, 1, 0, 1);

		.filter_select--open_state & {
			transition: max-height 1s ease-in-out;

			max-height: 100rem;
		}
	}
}

.filter_select__menu_in {
	@include transition_transform;

	@include bp($point_2, min) {
		padding-bottom: 1rem;

		background-color: $beige;

		transform: translateY(calc(-100% + 1px));
		border-bottom: 1px solid $black;

		pointer-events: none;

		.filter_select--open_state & {
			transform: translateY(0);
			pointer-events: auto;
		}
	}

	@include bp($point_3) {
		padding-top: 2rem;
		padding-bottom: 2rem;
	}
}

.filter_select__checkbox {
	font-size: 1.4rem;
	line-height: 1;

	color: $black;

	.form_checkbox__label {
		position: relative;

		font-family: $font_1;

		@include bp($point_2, min) {
			padding-left: 1.8rem;

			letter-spacing: -0.05em;
		}

		@include bp($point_3) {
			padding-left: 2.2rem;
		}

		&::after,
		&::before {
			top: 50%;
			transform: translateY(-50%);

			width: 2rem;
			height: 100%;

			border: none;

			@include bp($point_2, min) {
				font-size: 1.4rem;
			}

			@include bp($point_3) {
				font-size: 1.6rem;
			}
		}

		&::after {
			opacity: 1;
			content: '( )';
		}

		&::before {
			opacity: 0;
			content: '(•)';
		}
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 0.8rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.2rem;
		}
	}
}

.filter_select__checkbox_input {
	position: absolute;

	margin: 0;

	opacity: 0;

	&:checked {
		& + .form_checkbox__label {
			&::after {
				opacity: 0;
			}
			&::before {
				opacity: 1;
			}
		}
	}
}

.filter__btn_w {
	text-align: center;
}

.filter__btn {
	@include btn_size_normal;
	@include btn_style_white_border;

	width: 100%;

	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3, $point_6) {
		max-width: 30rem;
	}
}

.filter__btn_icon {
	width: 1.8rem;
	height: 1.8rem;

	margin-right: 1rem;
}
