@mixin h1 {
	font-weight: 300;
	font-family: $modern;
	font-style: italic;

	.body--black_mod & {
		color: $white;
	}

	@include bp($point_2, min) {
		font-size: 5rem;
		line-height: 1;
	}

	@include bp($point_3) {
		font-size: 3rem;
		line-height: 1.15;
	}
}

@mixin h2 {
	font-weight: 300;
	font-family: $modern;
	font-style: italic;

	.body--black_mod & {
		color: $white;
	}

	@include bp($point_2, min) {
		font-size: 4rem;
		line-height: 1;
	}

	@include bp($point_3) {
		font-size: 2.4rem;
		line-height: 1.4;
	}
}

@mixin h3 {
	line-height: 1.4;
	font-weight: 300;
	font-family: $modern;
	font-style: italic;

	@include bp($point_2, min) {
		font-size: 3rem;
	}

	@include bp($point_3) {
		font-size: 1.8rem;
	}
}

