@mixin h5 {
	// font-size: 2.4rem;
}

@mixin h6 {
	// font-size: 2rem;
}

@mixin text {
	font-weight: 300;

	.body--black_mod & {
		color: $white;
	}

	@include bp($point_2, min) {
		font-size: 2rem;
		line-height: 1.4;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
		line-height: 1.37;
	}
}

@mixin text_2 {
	font-weight: 400;
	letter-spacing: 0.01em;

	.body--black_mod & {
		color: $white;
	}

	@include bp($point_2, min) {
		font-size: 1.6rem;
		line-height: 1;
	}

	@include bp($point_3) {
		font-size: 1.4rem;
		line-height: 1.4;
	}
}

@mixin text_3 {
	font-size: 1.4rem;
	line-height: 1.14;
	font-weight: 400;
	letter-spacing: 0.01em;
}
// offsets

@mixin title_offset {
	margin-bottom: 2rem;
}
